@import "ScssHelpers/helpers-all.scss";

.get_started {
	@include bp($point_2, min) {
		padding-top: 8.4rem;
		padding-bottom: 6.1rem;
	}

	@include bp($point_3, $point_6) {
		padding-top: 6.1rem;
		padding-bottom: 4.8rem;
	}

	@include bp($point_6 - 1) {
		padding: 6.1rem 2rem 4.8rem;
	}
}

.get_started__content {
	margin: 0 auto;

	text-align: center;

	@include bp($point_2, min) {
		max-width: 98.6rem;
	}

	@include bp($point_3) {
		max-width: 50rem;
	}
}

.get_started__title {
	text-transform: uppercase;
	letter-spacing: 0.018em;

	@include h3();

	@include bp($point_2, min) {
		line-height: 1.3;
	}

	@include bp($point_3) {
		font-size: 3rem;
		line-height: 1.1;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 3.8rem;
		}

		@include bp($point_3) {
			margin-bottom: 4.2rem;
		}
	}
}

.get_started__btn {
	min-width: 16.4rem;

	@include btn_style_green_3();

	@include btn_size_normal();
}
