.ui_buttons {
	margin: 0 -2rem -2rem 0;

	@include flex_block(row, wrap, flex-start, center, center);
}

.ui_buttons__item {
	padding: 0 2rem 2rem 0;
}

.ui_buttons__btn {
	&.section_ui__btn--mod_1 {
		@include btn_style_white;
		@include btn_size_normal;
	}

	&.section_ui__btn--mod_2 {
		@include btn_style_orange;
		@include btn_size_normal;
	}

	&.section_ui__btn--mod_3 {
		@include btn_style_green;
		@include btn_size_normal;
	}

	&.section_ui__btn--mod_4 {
		@include btn_style_green_2;
		@include btn_size_normal;
	}

	&.section_ui__btn--mod_5 {
		@include btn_style_green_3;
		@include btn_size_normal;
	}

	&.section_ui__btn--mod_6 {
		@include btn_style_black;
		@include btn_size_normal;
	}

	&.section_ui__btn--mod_7 {
		@include btn_style_orange_3;
		@include btn_size_normal;
	}
}
