@import 'ScssHelpers/helpers-all.scss';

.faq_section {
	background-color: $black_2;

	@include bp($point_4, min) {
		padding: 8.6rem 3.2rem 10.6rem;
	}

	@include bp($point_4 - 1) {
		padding: 4rem 1.6rem 4.4rem;
	}
}

.faq_section__in {
	max-width: 94.6rem;
}

.faq_section__title {
	@include h3;

	color: $white;
	line-height: 1.33;
	text-align: center;

	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-bottom: 7.1rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 3.9rem;
		}
	}
}

.faq_section__item {
	position: relative;

	border-bottom: 1px solid $gray;

	&:first-child {
		border-top: 1px solid $gray;
	}
}

.faq_section__item_head {
	width: 100%;
	@include flex_block(row, nowrap, flex-start, center);

	cursor: pointer;
	transition: all 0.3s ease-in-out;

	@include bp($point_4, min) {
		padding: 1.7rem 1.5rem 2.2rem 0;

		.faq_section__item--active_state & {
			padding-top: 3.3rem;
			padding-bottom: 1.9rem;
		}
	}

	@include bp($point_4 - 1) {
		padding: 2.4rem 3.2rem 2.8rem 1.4rem;

		.faq_section__item--active_state & {
			padding-top: 1.7rem;
			padding-bottom: 3.2rem;
		}
	}
}

.faq_section__item_title {
	font-weight: 700;
	line-height: 1.3;
	color: $white;
	letter-spacing: 0.028em;

	transition: color 0.3s ease;

	&:not(:last-child) {
		margin-right: 2.6rem;
	}

	&:hover,
	&:focus {
		color: $green;
	}

	.faq_section__item--active_state & {
		color: $green;
	}

	@include bp($point_4, min) {
		font-size: 2.4rem;
	}

	@include bp($point_4 - 1) {
		font-size: 1.8rem;
	}
}

.faq_section__item_icon {
	flex-shrink: 0;
	width: 1.6rem;
	height: 1.4rem;

	margin-left: auto;

	color: $white;
	transform: rotate(-90deg);

	.faq_section__item--active_state & {
		transform: rotate(0deg);
	}

	@include transition_transform;

	@include bp($point_4, min) {
		margin-top: 0.4rem;
	}
}

.faq_section__item_body {
	max-height: 0;
	opacity: 0;

	transition: all 0.25s ease-out;
	will-change: max-height;
	overflow: hidden;
	pointer-events: none;

	.faq_section__item--active_state & {
		opacity: 1;
		pointer-events: auto;
	}
}

.faq_section__item_wrap {
	@include bp($point_4, min) {
		padding-bottom: 3.9rem;
	}

	@include bp($point_4 - 1) {
		padding: 0 3.2rem 2.8rem 1.4rem;
	}
}

.faq_section__item_text {
	font-size: 1.8rem;
	font-weight: 400;
	line-height: 1.43;
	letter-spacing: 0.028em;
	color: $white;

	p:not(:last-child) {
		margin-bottom: 1rem;
	}
}
