@mixin h1 {
	font-weight: 700;

	@include bp($point_2, min) {
		font-size: 6rem;
	}

	@include bp($point_3) {
		font-size: 3rem;
	}
}

@mixin h2 {
	font-weight: 700;

	@include bp($point_2, min) {
		font-size: 5.5rem;
	}

	@include bp($point_3) {
		font-size: 3rem;
	}
}

@mixin h3 {
	font-weight: 700;

	@include bp($point_2, min) {
		font-size: 5rem;
	}

	@include bp($point_3) {
		font-size: 3rem;
	}
}

@mixin h4 {
	font-weight: 700;

	@include bp($point_2, min) {
		font-size: 4.8rem;
	}

	@include bp($point_3) {
		font-size: 2.4rem;
	}
}

@mixin h5 {
	@include bp($point_2, min) {
		font-size: 3rem;
	}

	@include bp($point_3) {
		&:not(.customer_stories_studies__title_content--big_mod) {
			font-size: 1.8rem;
		}
		&.customer_stories_studies__title_content--big_mod {
			font-size: 2.4rem;
		}
	}
}

@mixin h6 {
	font-size: 2rem;
}

@mixin text {
	font-size: 1.6rem;
}

@mixin title {
	font-weight: 700;

	@include bp($point_2, min) {
		font-size: 3rem;
	}

	@include bp($point_3) {
		font-size: 2.4rem;
	}
}

@mixin description {
	font-weight: 400;

	@include bp($point_2, min) {
		font-size: 1.8rem;
	}

	@include bp($point_3) {
		font-size: 1.6rem;
	}
}
// offsets

@mixin title_offset {
	margin-bottom: 2rem;
}