@import 'ScssHelpers/helpers-all.scss';

.lang_menu {
	position: absolute;
	width: 100%;
	min-width: 6.4rem;
	z-index: 20;

	@include bp($point_1, min) {
		top: 118%;
		left: 0;
		height: auto;

		padding: 0.4rem;

		border-radius: 2rem;
		border: 1px solid $gray_1;
		box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.227);
		background-color: $gray_1;

		opacity: 0;
		transform: translate(-3px, -1rem);
		pointer-events: none;

		transition: transform 0.3s ease, opacity 0.3s ease;

		.header__langues:hover &,
		.header__langues:focus & {
			opacity: 1;
			transform: translate(-3px, 0);
			pointer-events: initial;
		}
	}

	@include bp($point_1 - 1) {
		top: 0;
		left: 0;

		height: var(--full-screen);
		z-index: 101;

		display: flex;
		justify-content: flex-start;
		flex-direction: column;

		background-color: $black_2;

		transform: translateX(100%);
		visibility: hidden;

		transition: all 0.3s ease-in-out;

		&.lang_menu--open_state {
			transform: translateX(0);
			visibility: visible;
		}
	}
}

.lang_menu__wrap {
	@include bp($point_1, min) {
		display: none;
	}

	@include bp($point_1 - 1) {
		position: relative;
		padding: 3.3rem 5.4rem 4rem;

		&::after {
			position: absolute;
			bottom: 0;
			left: 0;

			content: '';

			width: 100%;
			height: 1px;

			background-color: $white;
			opacity: 0.1;
		}
	}
}

.lang_menu__close {
	display: flex;
	align-items: center;
	flex-direction: row-reverse;
	color: $green;
	font-size: 1.8rem;
	font-family: $font_1;
	font-weight: 700;
	letter-spacing: 0.02em;
}

.lang_menu__close_icon {
	width: 1.4rem;
	height: 1.4rem;
	color: $green;

	margin-right: 1rem;
}

.lang_menu__list {
	@include bp($point_1 - 1) {
		height: 100%;

		padding-bottom: 3.2rem;
		overflow-y: auto;
	}
}

.lang_menu__item {
	display: flex;
	align-items: center;
	justify-content: flex-start;

	&:not(:last-child) {
		@include bp($point_1, min) {
			margin-bottom: 3px;
		}
	}
}

.lang_menu__link {
	display: flex;
	align-items: center;

	&:hover,
	&:focus {
		text-decoration: none;

		@include bp($point_1, min) {
			background-color: $gray;
		}

		@include bp($point_1 - 1) {
			background-color: rgba(255, 255, 255, 0.1);
		}
	}

	@include bp($point_1, min) {
		justify-content: center;
		text-align: center;

		width: 100%;
		height: 3.5rem;

		border-radius: 2.7rem;
	}

	@include bp($point_1 - 1) {
		position: relative;
		padding: 1.8rem 3.6rem 1.8rem 5.2rem;
		width: 100%;

		font-size: 1.6rem;
		color: $white;
		letter-spacing: 0;
		text-align: left;

		&::after {
			position: absolute;
			bottom: 0;
			left: 0;

			content: '';

			width: 100%;
			height: 1px;

			background-color: $white;
			opacity: 0.1;
		}

		&:not(:last-child) {
			margin-bottom: 0.6rem;
		}

		&:focus {
			text-decoration: none;
			background-color: rgba(255, 255, 255, 0.1);
		}
	}
}

.lang_menu__link_title {
	font-weight: 400;

	.lang_menu__item--active_mod & {
		font-weight: 700;
	}

	&.lang_menu__link_title--dec_mod {
		@include bp($point_1 - 1) {
			display: none;
		}
	}

	&.lang_menu__link_title--mob_mod {
		@include bp($point_1, min) {
			display: none;
		}
	}

	@include bp($point_1, min) {
		text-transform: uppercase;
		font-size: 1.2rem;
		color: $black_2;
	}

	@include bp($point_1 - 1) {
		font-size: 1.6rem;
		color: $white;
	}
}
