@import 'ScssHelpers/helpers-all.scss';

.customer_one {
	background-color: $black_2;

	@include bp($point_2, min) {
		padding: calc(var(--height-header) + 0.5rem) 3.2rem 0;
	}

	@include bp($point_3) {
		padding: calc(var(--height-header) + 3.1rem) 3.2rem 0;
	}
}

.customer_one_in {
	max-width: 126.9rem;
	width: 100%;
	margin: 0 auto;
}

.customer_one__body {
	display: flex;

	@include bp($point_2, min) {
		margin: 0 -3rem 0 0;
	}

	@include bp($point_3) {
		flex-direction: column;
	}
}

.customer_one__info {
	@include bp($point_2, min) {
		flex: 0 0 33.4%;

		margin: 4rem 0;
		padding: 0 3rem 0 0;
	}

	@include bp($point_3) {
		padding: 0 0 4.4rem 0;
	}
}

.customer_one__image {
	@include bp($point_2, min) {
		align-self: end;
		flex: 0 0 66.6%;

		padding: 0 3rem 0 0;
	}
}

.customer_one__title {
	color: $white;
	text-transform: uppercase;
	font-weight: 700;

	@include bp($point_2, min) {
		margin-bottom: 3.3rem;

		font-size: 3rem;
		line-height: 1.43;

		letter-spacing: 0.01em;
	}

	@include bp($point_3) {
		margin-bottom: 3.7rem;

		font-size: 2.4rem;
		line-height: 1.25;

		letter-spacing: 0.03em;
	}
}

.customer_one__action {
	color: $green;

	cursor: pointer;

	transition: color 0.3s ease;

	&:hover {
		color: $white;
	}
}

.customer_one__icon {
	width: 2.5rem;
	height: 2.5rem;

	@include bp($point_2, min) {
		margin-right: 1.9rem;
	}

	@include bp($point_3) {
		margin-right: 1.4rem;
	}
}

.customer_one__link {
	display: inline-flex;
	flex-direction: row-reverse;
	align-items: center;

	font-weight: 700;
	color: inherit;

	&:hover {
		text-decoration: none;
	}

	@include bp($point_2, min) {
		font-size: 1.8rem;
	}

	@include bp($point_3) {
		font-size: 1.6rem;
	}
}

.customer_one__picture {
	position: relative;

	padding-bottom: 48%;
	display: block;

	line-height: 1;
	font-size: 0;
}

.customer_one__img {
	position: absolute;
	top: 0;
	left: 0;

	display: block;

	width: 100%;
	height: 100%;

	object-fit: contain;
	object-position: 50% 100%;

	border-radius: 0.8rem;
}
