//------------------------------------------------------------layout
.header {
	position: fixed;
	top: 0;
	left: 0;

	height: var(--height-header);
	width: 100%;

	font-size: 1rem;

	will-change: transform;
	transition: transform 0.3s ease-in-out, background 0.3s ease-in-out;

	@include z-index(header);

	&:not(.body--header_green_mod &, .body--header_transparent &) {
		background-color: $orange;
	}

	.body--header_transparent & {
		background-color: transparent;
	}

	.body--header_green_mod & {
		background-color: $black_2;
	}

	// &.header--scroll_state {
	// 	box-shadow: 0 1px 1rem rgba($black, 0.2);

	// 	.body--header_transparent & {
	// 		background-color: $black_2;
	// 	}
	// }

	// body:not(.body--open_menu_state) &.header--pos_state {
	// 	transform: translateY(-100%);
	// }

	body:not(.body--open_menu_state) &.header--scroll_state {
		transform: translateY(-100%);
	}

	&.header--shadow_mod {
		box-shadow: 0 1px 1rem rgba($black, 0.2);

		.body--header_transparent & {
			background-color: $black_2;
		}
	}

	&.header--locked_state {
		@include bp($point_2, min) {
			transform: translateY(-100%);
		}
	}

	@include bp($point_1, min) {
		padding: 0 4rem;

		&::after {
			content: '';
			position: absolute;
			top: 0;
			left: 0;

			z-index: 100;

			width: 100vw;
			height: var(--full-screen);

			opacity: 0;
			visibility: hidden;

			background-color: rgba(23, 43, 41, 0.486687);

			transition: opacity 0.3s ease;

			.body--open_submenu_state & {
				opacity: 1;
				visibility: visible;
			}
		}
	}

	@include bp($point_1 - 1) {
		padding: 0 3.2rem;
	}
}

//------------------------------------------------------------layout###

.header__box {
	height: 100%;
}

.header__in {
	max-width: 151.2rem;
	width: 100%;
	height: 100%;
	margin: 0 auto;
}

.header__wrap {
	display: flex;
	align-items: center;
	height: 100%;
}

.header__logo {
	flex-shrink: 0;
	width: 21.7rem;
	height: 5rem;

	&:not(:last-child) {
		margin-right: 1rem;
	}
}

.header__logo_img {
	@include contain_img;
}

.header__menu_body {
	display: flex;
	align-items: center;

	@include bp($point_1, min) {
		margin-left: auto;
	}

	@include bp($point_1 - 1) {
		position: fixed;

		top: 0;
		left: 0;
		z-index: 99;

		height: var(--full-screen);
		width: 100%;

		flex-direction: column;
		background-color: $black_2;

		transform: translateX(100%);
		visibility: hidden;

		transition: all 0.3s ease-in-out;

		.body--open_menu_state & {
			transform: translateX(0);
			visibility: visible;
		}
	}
}

.header__menu_wrap {
	display: flex;
	align-items: center;

	@include bp($point_1 - 1) {
		flex-direction: column;
		width: 100%;
		height: 100%;
		overflow-y: auto;
	}
}

.header__menu_in {
	&:not(:last-child) {
		@include bp($point_1, min) {
			margin-right: 2.8rem;
		}

		@include bp($point_1 - 1) {
			margin-bottom: 5.2rem;
		}
	}

	@include bp($point_1 - 1) {
		width: 100%;
	}
}

.header__menu_top {
	@include bp($point_1, min) {
		display: none;
	}

	@include bp($point_1 - 1) {
		position: relative;
		width: 100%;
		display: flex;
		align-items: center;

		padding: 2.3rem 3.2rem;

		&::before {
			position: absolute;
			bottom: 0;
			left: 0;

			content: '';

			width: 100%;
			height: 1px;

			background-color: $white;
			opacity: 0.1;
		}
	}
}

.header__logo_mob {
	width: 4.6rem;
	height: 5rem;
}

.header__menu_close {
	position: absolute;
	top: 50%;
	right: var(--gl-indent);

	height: 1.6rem;
	width: 2rem;

	margin-top: -1.2rem;
	margin-right: -1px;
	text-transform: uppercase;

	color: transparent;
	cursor: pointer;

	&:before {
		content: '';
		position: absolute;
		top: 50%;
		left: 0;
		width: 100%;
		height: 0.2rem;

		background: $white;
		transform: (rotate(45deg) translate(0, 0));
	}
	&:after {
		content: '';
		position: absolute;
		top: 50%;
		left: 0;
		width: 100%;
		height: 0.2rem;

		background: $white;
		transform: (rotate(-45deg) translate(0, 0));
	}
}

.header_menu__list {
	@include bp($point_1, min) {
		display: flex;
		align-items: center;
		flex-wrap: wrap;

		margin: 0 -3.2rem -1.4rem 0;
		font-size: 1.4rem;
	}
}

.header_menu__item {
	&:not(&.submenu_select--open_state) {
		&:hover,
		&:focus {
			@include bp($point_1 - 1) {
				background-color: rgba(255, 255, 255, 0.1);
			}
		}
	}

	@include bp($point_1, min) {
		padding: 0 3.2rem 1.4rem 0;
	}

	@include bp($point_1 - 1) {
		position: relative;
		z-index: 2;
		padding: 1.7rem 0;
		width: 100%;

		transition: padding 0.3s ease;

		&::after {
			position: absolute;
			bottom: 0;
			left: 0;

			content: '';

			width: 100%;
			height: 1px;

			background-color: $white;
			opacity: 0.1;
		}

		&.submenu_select--open_state {
			padding-bottom: 0;
		}
	}
}

.header_menu__wrap {
	position: relative;
}

.header_menu__link {
	align-items: center;

	color: $white;
	font-weight: 400;
	letter-spacing: 0.04em;
	line-height: 100%;
	font-family: $font_1;

	transition: font-weight 0.3s ease;
	cursor: pointer;

	&::after {
		content: attr(data-text);
		content: attr(data-text) / '';
		height: 0;
		visibility: hidden;
		overflow: hidden;
		user-select: none;
		pointer-events: none;
		font-weight: 700;

		@media speech {
			display: none;
		}
	}

	&:focus {
		text-decoration: none;
	}

	&:hover {
		text-decoration: none;
		@include bp($point_1, min) {
			font-weight: 700;
		}
	}

	.header_menu__item--active_mod & {
		font-weight: 700;
		line-height: 100%;
	}

	@media not all and (min-resolution: 0.001dpcm) {
		@supports (-webkit-appearance: none) {
			.header_menu__item--active_mod:-webkit-full-screen & {
				margin-top: 0;
			}
		}
	}

	@include bp($point_1, min) {
		// font-size: 1.4rem;
		display: inline-flex;
		flex-direction: column;

		justify-content: space-between;
	}

	@include bp($point_1 - 1) {
		position: relative;
		display: flex;
		width: 100%;
		padding: 0 3.6rem 0 3.2rem;

		font-size: 1.6rem;
		letter-spacing: 0;
	}
}

.header_menu__icon_wrap {
	position: absolute;
	top: 50%;

	transform: translateY(-50%);

	pointer-events: none;

	color: $white;

	transition: transform 0.3s ease-in-out;

	@include bp($point_1, min) {
		right: -1.2rem;

		width: 0.7rem;
		height: 0.7rem;

		.body--open_submenu_state & {
			transform: translateY(-50%) rotate(90deg);
		}
	}

	@include bp($point_1 - 1) {
		right: 3.9rem;

		width: 1rem;
		height: 0.9rem;

		.submenu_select--open_state & {
			transform: translateY(-50%) rotate(90deg);
		}
	}
}

.header__info {
	display: flex;
	align-items: center;

	@include bp($point_1 - 1) {
		margin-top: auto;
		width: 100%;
		min-height: 6.5rem;

		border-top: 1px solid rgb(255, 255, 255, 0.1);
	}
}

.header__info__list {
	display: flex;
	align-items: center;
	height: 100%;

	&:not(:last-child) {
		@include bp($point_1, min) {
			margin-right: 3rem;
		}
	}

	&.header__info__list--desktop_mod {
		@include bp($point_1 - 1) {
			display: none;
		}
	}
	&.header__info__list--mob_mod {
		@include bp($point_1, min) {
			display: none;
		}
	}
}

.header__info__item {
	height: 100%;
	&:not(:last-child) {
		@include bp($point_1, min) {
			margin-right: 2.7rem;
		}
	}
}

.header__console_link {
	@include bp($point_1, min) {
		position: relative;
		z-index: 101;
		min-width: 10.9rem;
		@include btn_style_white_2;
		@include btn_size_small;
	}
}

.header__user {
	&:hover,
	&:focus {
		background-color: $white;
		color: $orange;

		.body--header_green_mod &,
		.body--header_transparent & {
			color: $black_2;
		}
	}

	@include bp($point_1, min) {
		display: block;
		width: 3.5rem;
		height: 3.5rem;

		color: $white;
		border-radius: 50%;

		transition: color 0.3s ease, background 0.3s ease;
	}
}

.header__info_link {
	@include bp($point_1 - 1) {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		height: 100%;
		padding: 1rem 3rem;
		text-align: center;

		color: $green;
		font-size: 1.6rem;
		font-weight: 400;

		border-right: 1px solid rgb(255, 255, 255, 0.1);
	}
}

.header__langues {
	@include bp($point_1, min) {
		position: relative;
	}
}

.header__langues_btn {
	@include bp($point_1, min) {
		position: relative;
		z-index: 99;
		min-width: 5.7rem;

		@include btn_style_transparent;
		@include btn_size_small;

		&::after {
			content: '';
			position: absolute;
			top: 100%;
			left: 0;

			width: 100%;
			height: 1rem;
		}
	}

	@include bp($point_1 - 1) {
		display: flex;
		align-items: center;
		justify-content: center;
		text-align: center;
		padding: 1.8rem 3rem 1.8rem;

		color: $green;
		font-size: 1.6rem;
		font-weight: 400;
		font-family: $font_1;
	}
}

.header__langues_arrow {
	@include bp($point_1, min) {
		display: none;
	}

	@include bp($point_1 - 1) {
		width: 1rem;
		height: 1rem;
		margin-left: 1.2rem;
		margin-bottom: 0.4rem;
	}
}

//---------------------------------------------trigger

@mixin user_menu_butt_line {
	position: absolute;
	top: 50%;
	left: 0;
	width: 100%;
	height: 0.2rem;

	background: $white;
}

.header_trigger {
	@include bp($point_1, min) {
		display: none;
	}

	@include bp($point_1 - 1) {
		position: absolute;
		top: 50%;
		right: 2.2rem;

		height: 1.6rem;
		width: 2.2rem;

		margin-top: -2rem;
		margin-right: 0.9rem;
		text-transform: uppercase;

		color: transparent;
		cursor: pointer;

		&:before {
			content: '';
			transform: translate(0, -0.7rem);
			transition: all 0.3s ease;

			@include user_menu_butt_line;
		}
		&:after {
			content: '';
			transform: translate(0, 0.7rem);
			transition: all 0.3s ease;

			@include user_menu_butt_line;
		}
	}
}

.header_trigger__decor {
	transition: all 0.3s ease;

	@include user_menu_butt_line;
}
