@import "ScssHelpers/helpers-all.scss";

.cards_requests {
	display: flex;
	align-items: stretch;
	justify-content: center;

	flex-wrap: wrap;

	color: $white;

	@include bp($point_2, min) {
		margin: 0 -4.5rem -4.5rem 0;
	}

	@include bp($point_3) {
		margin: 0 -3.3rem -3.3rem 0;
	}
}

.cards_requests__wrapper {
	flex: 0 1 47.4rem;

	@include bp($point_2, min) {
		padding: 0 4.5rem 4.5rem 0;
	}

	@include bp($point_3) {
		padding: 0 3.3rem 3.3rem 0;
	}
}

.cards_requests__card {
	height: 100%;

	border: 1px solid $gray;

	transition: all 0.3s ease;

	@include bp($point_2, min) {
		padding: 4.5rem 7.8rem 6.1rem 8.3rem;

		&:hover,
		&:focus {
			background-color: $white;
			color: $black_2;
		}
	}

	@include bp($point_3) {
		padding: 3.8rem 4rem 4.8rem 4.16rem;
	}
}

.cards_requests__title {
	letter-spacing: 0.02em;
	font-size: 3rem;
	font-weight: 700;

	&:not(:last-child) {
		margin-bottom: 3rem;
	}
}

.cards_requests__text {
	font-size: 1.6rem;
	letter-spacing: 0.02em;
	line-height: 1.25;
	font-weight: 400;

	&:not(:last-child) {
		margin-bottom: 3.7rem;
	}
}

.cards_requests__btn {
	color: $green;

	transition: color 0.3s ease;

	@include bp($point_2, min) {
		&:hover,
		&:focus {
			color: $black_2;
		}
	}
}

.cards_requests__link {
	display: flex;
	align-items: center;

	color: inherit;
	font-size: 1.8rem;
	font-weight: 700;

	&:hover,
	&:focus {
		text-decoration: none;
	}
}

.cards_requests__icon {
	display: block;

	width: 1.3rem;
	height: 2.4rem;

	margin-left: 1.2rem;
}

.customer_requests--light_mod .cards_requests__card {
	@include bp($point_2, min) {
		&:hover,
		&:focus {
			background-color: $gray_1;
			border-color: transparent;
		}
	}
}

.customer_requests--light_mod .cards_requests {
	color: $black_2;
}


.customer_requests--light_mod .cards_requests__card {
	@include bp($point_2, min) {
		&:hover,
		&:focus {
			background-color: $gray_1;
		}
	}
}