@import 'ScssHelpers/helpers-all.scss';

.features__content {
	max-width: 100%;
	margin: 0 auto;

	.pricing_tabs__tab_content[id='tab-0'] & {
		@include bp($point_2, min) {
			max-width: 91rem;
		}
	}

	.pricing_tabs__tab_content[id='tab-1'] & {
		@include bp($point_2, min) {
			// margin-left: -3.2rem;
			//margin-right: -1rem;
			padding-right: 2px;
		}
	}
}

.features__list_w {
}

.features__list_content {
	position: relative;

	@include bp($point_2, min) {
		width: 100%;
	}

	@include bp($point_3) {
		margin: 0 calc(var(--gl-indent) * -1);
	}
}

.features__list_content table {
	position: relative;
	display: block;
	// max-height: 0;

	opacity: 0;
	overflow: hidden;
	pointer-events: none;
	transition: all 0.25s ease-out;
	will-change: max-height;
	z-index: 1;
}

.features__list_content--active_state table {
	@include bp($point_3) {
		margin-bottom: 2rem;
	}

	opacity: 1;
	pointer-events: auto;
}

.features__list_content tbody {
	display: block;
}

.features__list_content tr {
	display: flex;
	flex-wrap: wrap;
}

.features__list_title_wrap {
	@include flex_block(row, wrap, flex-start, baseline, center);
	position: relative;
	width: calc(100% + 2px);
	// width: 100%;

	background-color: $white;
	z-index: 2;
	cursor: pointer;

	.features__list_content:not(.features__list_content--active_state) & {
		position: static !important;
		transform: none !important;
	}

	@include bp($point_2, min) {
		height: 6.8rem;
		border-bottom: 1px solid $gray;

		.features__list_content--active_state & {
			border-bottom: none;
		}

		&:not(:last-child) {
			//margin-bottom: 1.6rem;
		}

		//&:after {
		//	position: absolute;
		//	bottom: 0;
		//	left: 0;
		//
		//	display: block;
		//	width: 100%;
		//	height: 1px;
		//	background-color: $gray;
		//
		//	content: '';
		//}
	}

	@include bp($point_3) {
		padding: 2rem var(--gl-indent);
	}
}

.features__list_title_overlay {
	position: absolute;
	bottom: 100%;
	left: 0;

	z-index: 10;
	width: 100%;
	height: 100vh;
	background-color: $white;
	// pointer-events: none;
	pointer-events: none;
	cursor: auto;

	opacity: 0;

	@include bp($point_3) {
		display: none;
	}

	.features__list_content--fixed_state & {
		opacity: 1;
		pointer-events: initial;
	}
}

.features__list_title {
	font-weight: 700;

	margin-right: 2rem;

	cursor: pointer;

	@include bp($point_2, min) {
		//min-height: 6.6rem;
		//line-height: 6.6rem;
		font-size: 2.4rem;
	}

	@include bp($point_3) {
		//min-height: 4rem;
		font-size: 2.1rem;
	}
}

.features__list_title_icon {
	width: 1.8rem;
	height: 1.8rem;

	color: $black_2;

	transform: rotate(-90deg);
	transition: transform 0.3s ease;

	@include bp($point_3) {
		margin-left: auto;
	}

	.features__list_content--active_state & {
		transform: rotate(0deg);
	}
}

.features__list_col {
	@include flex_block(row, wrap, flex-start, stretch, flex-start);
}

.features__list_content td {
	position: relative;

	font-size: 1.2rem;
	font-weight: 700;

	border-right: 1px solid $gray;

	@include bp($point_2, min) {
		min-height: 5.7rem;
	}

	@include bp($point_2 - 1) {
		text-align: center;
	}

	&:before,
	&:after {
		position: absolute;
		left: 50%;
		z-index: 0;

		width: 92%;
		height: 0.1rem;

		background-color: $gray;
		transform: translateX(-50%);
		content: '';

		@include bp($point_3) {
			display: none;
		}
	}

	&:before {
		bottom: 0;
	}

	&:after {
		top: 0;
		display: none;
	}

	&:first-child {
		@include flex_block(row, wrap, flex-start, center, center);

		font-size: 1.4rem;

		&:before,
		&:after {
			left: 0;
			width: 96%;
			transform: translateX(0);
		}

		@include bp($point_2, min) {
			padding: 1rem 1.6rem 1rem 0;
		}

		@include bp($point_2 - 1) {
			padding: 1.6rem 4.6rem 1rem;
		}

		@include bp($point_3) {
			width: 100%;
			min-height: 4.5rem;

			justify-content: center;

			font-weight: 400;

			background-color: $gray_1;
			border: none;
		}

		.pricing_tabs__tab_content[id='tab-0'] & {
			@include bp($point_2, min) {
				width: 33%;
			}
		}

		.pricing_tabs__tab_content[id='tab-1'] & {
			@include bp($point_2, min) {
				width: 26.4%;
			}
		}
	}

	&:not(:first-child) {
		@include flex_block(row, wrap, center, center, center);
		flex: 1 0 0;

		padding: 1rem 1.6rem;

		@include bp($point_3) {
			min-height: 5.6rem;
		}
	}

	&:nth-child(2) {
		.pricing_tabs__tab_content[id='tab-0'] & {
			@include bp($point_2, min) {
				background-color: $gray_1;
			}
		}
	}

	&:nth-child(4) {
		.pricing_tabs__tab_content[id='tab-1'] & {
			@include bp($point_2, min) {
				background-color: $gray_1;
			}
		}
	}

	&:last-child {
		@include bp($point_3) {
			border: none;
		}
	}

	.features__list_content tr:last-child & {
		&:not(:first-child) {
			@include bp($point_2, min) {
				border-bottom: 1px solid $gray;
			}
		}
	}
}

.features__list_content tr:first-child td:first-child:after {
	@include bp($point_2, min) {
		display: block;
	}
}

.features__list_content tr:first-child td:not(:first-child) {
	@include bp($point_2, min) {
		border-top: 1px solid $gray;
	}
}

.features__list_content tr:last-child td:not(:first-child):before {
	display: none;
}

.features__list_content tr:last-child td:not(:first-child) {
	@include bp($point_2, min) {
		border-bottom: 1px solid $gray;
	}
}

.features__col_icon_w {
	display: inline-block;
	width: 2rem;
	height: 2rem;

	color: $green;
}
