@import "ScssHelpers/helpers-all.scss";

.customer_numbers {
	@include bp($point_2, min) {
		padding: 6.5rem 3.2rem 4.7rem 3.2rem;
	}

	@include bp($point_3) {
		padding: 5.3rem 3.2rem 4.2rem 3.2rem;
	}
}

.customer_numbers_in {
	max-width: 126.9rem;
	width: 100%;
	margin: 0 auto;
}

.customer_numbers__cards {
	display: flex;
	flex-wrap: wrap;
	align-items: stretch;
	justify-content: center;

	@include bp($point_2, min) {
		margin: 0 -3rem -3rem 0;
	}

	@include bp($point_3) {
		margin: 0 -1.5rem -1.5rem 0;

		flex-direction: column;
	}
}

.customer_numbers__item {
	flex: 0 0 33.333%;

	@include bp($point_2, min) {
		padding: 0 3rem 3rem 0;
	}

	@include bp($point_3) {
		padding: 0 1.5rem 1.5rem 0;
	}

}

.customer_numbers__card {
	padding: 2.5rem 2.3rem 2.8rem;

	height: 100%;


	border: 1px solid $gray;
	border-radius: 0.8rem;

	text-align: center;

	@include bp($point_2, min) {
		min-height: 17.7rem;
	}

	@include bp($point_3) {
		min-height: 15.5rem;
	}
}

.customer_numbers__percent {
	margin-bottom: 0.8rem;

	font-size: 5rem;
	font-weight: 700;

	span {
		font-size: 3rem;
	}
}

.customer_numbers__text {
	font-size: 1.8rem;
}