@import 'ScssHelpers/helpers-all.scss';

.primary_products {
	@include bp($point_2, min) {
		padding: 6.9rem 2.2rem 8.4rem 2.2rem;
	}

	@include bp($point_3) {
		padding: 4.8rem 2.2rem 3rem 2.2rem;
	}
}

.primary_products_in {
	max-width: 127.6rem;
	width: 100%;
	margin: 0 auto;
}

.primary_products__top {
	text-align: center;

	@include bp($point_2, min) {
		margin-bottom: 7.1rem;
	}

	@include bp($point_3) {
		margin-bottom: 4.8rem;
	}
}

.primary_products__title {
	text-transform: uppercase;
	@include h3;

	@include bp($point_1, $point_2) {
		font-size: 4rem;
	}

	@include bp($point_2, min) {
		padding: 0 20rem;
		margin-bottom: 4rem;

		line-height: 1.3;
	}

	@include bp($point_3) {
		padding: 0 1rem;
		margin-bottom: 3.2rem;

		line-height: 1.1;
		text-align: left;
	}
}
