@import 'family';
@import 'reset';
@import 'fonts';
@import 'print';

//------------------------------------------------------------base styles
:root {
	--full-screen: calc(var(--vh, 1vh) * 100);

	--height-header: 9.5rem;
	--height-footer: 16.2rem;
	--gl-indent: 3.2rem;
}

html {
	height: 100%;
	font-family: $font_1;
	line-height: 1.33;

	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);

	@include bp($point_0 + 1, min) {
		font-size: 10px;
	}

	@include bp($point_0, $point_2) {
		font-size: calc(100vw / $design_width * 10);
	}

	@include bp($point_3, $point_9) {
		font-size: 10px;
	}

	@include bp($point_9 - 1) {
		font-size: calc(100vw / 414 * 10);
	}

	@media screen and (max-height: 500px) {
		font-size: 6px;
	}

	@include bp($point_7) {
		.customer_numbers__percent {
			font-size: 3rem;
		}
		.cards_requests__link {
			font-size: 1.4rem;
		}
	}
}

body {
	position: relative;

	display: flex;
	height: 100%;

	font-size: $font_size_base;
	color: $color_text_base;

	-webkit-text-size-adjust: none;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	background: $body_bg;

	@supports (display: grid) {
		display: block;
	}

	&.body--popup_open,
	&.body--open_menu_state,
	&.body--open_submenu_state {
		overflow: hidden;
	}

	&.body--popup_open {
		touch-action: none;
	}
}

.preloader {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	background-color: $white;

	opacity: 1;
	transition: opacity 0.3s;

	z-index: 1000;

	.body--loaded & {
		pointer-events: none;
		opacity: 0;
	}
}

.wrapper {
	position: relative;

	width: 100%;
	display: flex;
	flex-direction: column;
	min-height: 100%;
	height: auto !important;

	overflow-x: hidden;
}

.base {
	width: 100%;
	position: relative;
	// padding: var(--height-header) 0 0;

	flex: 1 0 auto;
}

a {
	text-decoration: none;
	color: $color_link_base;

	&:hover,
	&:focus {
		text-decoration: underline;
	}
}

// for wp
.block-preview-image {
	width: 100%;
	max-width: 100%;
	height: auto;
}

//------------------------------------------------------------base styles###
