@import 'ScssHelpers/helpers-all.scss';

.submenu {
	@include bp($point_1, min) {
		position: absolute;
		top: calc(100% + 0.8rem);
		left: 50%;

		height: auto;
		min-width: 28.2rem;
		padding-top: 1rem;

		transform: translateX(-50%);

		opacity: 0;
		pointer-events: none;
		transition: opacity 0.3s ease, transform 0.3s ease-in-out;

		z-index: 1000;

		&.submenu_open_state {
			opacity: 1;
			pointer-events: initial;
		}
	}
}

.submenu__wrap {
	@include bp($point_1, min) {
		position: relative;

		border-radius: 4px;
		background-color: $white;

		&::after {
			content: '';
			position: absolute;
			top: -0.5rem;
			left: 50%;

			height: 1.6rem;
			width: 1.6rem;

			transform: translateX(-50%) rotate(-45deg);

			background-color: $white;
		}
	}

	@include bp($point_1 - 1) {
		display: flex;
		justify-content: flex-start;
		flex-direction: column;

		width: 100%;
		max-height: 0;
		overflow: hidden;
		@include transition_all;
	}
}

.submenu__list {
	@include bp($point_1 - 1) {
		padding: 1.6rem 0;
	}
}

.submenu__item {
	position: relative;
	z-index: 5;
	display: flex;
	align-items: flex-start;
}

.submenu__link {
	position: relative;
	display: block;

	width: 100%;

	text-align: left;
	text-decoration: none;

	transition: background 0.3s ease;

	&:focus {
		text-decoration: none;
	}

	.submenu__item--active_mod & {
		color: $black;
		font-weight: 700;
	}

	&:hover,
	&:focus {
		text-decoration: none;

		@include bp($point_1, min) {
			background-color: $gray_1;
		}

		@include bp($point_1 - 1) {
			background-color: rgba(255, 255, 255, 0.1);
		}
	}

	.submenu__item--active_mod & {
		@include bp($point_1, min) {
			background-color: $gray_1;
		}

		@include bp($point_1 - 1) {
			background-color: rgba(255, 255, 255, 0.1);
		}
	}

	@include bp($point_1, min) {
		overflow: hidden;
		padding: 2.2rem 2.2rem 2.6rem;

		:first-child & {
			border-radius: 4px 4px 0 0;
		}

		.submenu__item:last-child & {
			border-radius: 0 0 4px 4px;
		}
	}

	@include bp($point_1 - 1) {
		padding: 1.6rem 3.6rem 1.6rem 3.2rem;
	}
}

.submenu__link_title {
	font-weight: 400;

	line-height: 1.5;

	&:not(:last-child) {
		@include bp($point_1, min) {
			margin-bottom: 1rem;
		}
	}

	.submenu__item--active_mod & {
		@include bp($point_1, min) {
			font-weight: 700;
		}
	}

	@include bp($point_1, min) {
		color: $black_2;
		font-size: 1.4rem;
	}

	@include bp($point_1 - 1) {
		color: $white;
		font-size: 1.6rem;
	}
}

.submenu__link_text {
	font-weight: 400;

	@include bp($point_1, min) {
		font-size: 1.2rem;
		color: $gray_2;
	}

	@include bp($point_1 - 1) {
		color: $gray_3;
		font-size: 1.4rem;
	}
}
