@import 'ScssHelpers/helpers-all.scss';

.offer_block {
	background-color: $gray_4;

	@include bp($point_4, min) {
		padding: 5.8rem 3.2rem 6.3rem;
	}

	@include bp($point_4 - 1) {
		padding: 4.4rem 3.2rem 6rem;
	}
}

.offer_block__in {
	max-width: 83.7rem;
	width: 100%;
	margin: 0 auto;
}

.offer_block__list {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;

	@include bp($point_4, min) {
		margin: 0 -3.7rem -2rem 0;
	}
}

.offer_block__item {
	height: 100%;

	display: flex;
	flex-direction: column;

	&:not(:last-child) {
		@include bp($point_4 - 1) {
			margin-bottom: 4.3rem;
		}
	}

	@include bp($point_4, min) {
		flex-shrink: 0;
		width: 50%;
		min-height: 27rem;

		padding: 0 3.7rem 2rem 0;
	}
}

.offer_block__item_title {
	font-size: 2.4rem;
	font-weight: 700;
	color: $black_2;

	text-transform: uppercase;
	line-height: 1.4;
	letter-spacing: 0.01em;

	&:not(:last-child) {
		margin-bottom: 2.9rem;
	}
}

.offer_block__item_text {
	line-height: 1.42;
	font-weight: 400;
	color: $black_2;

	&:not(:last-child) {
		margin-bottom: 3.2rem;
	}

	@include bp($point_4, min) {
		font-size: 1.8rem;
	}

	@include bp($point_4 - 1) {
		font-size: 1.6rem;
	}
}

.offer_block__item_link {
	position: relative;
	align-self: start;
	padding-right: 2rem;
	margin-top: auto;

	font-weight: 700;
	color: $green;

	@include bp($point_4, min) {
		font-size: 1.8rem;
	}

	@include bp($point_4 - 1) {
		font-size: 1.6rem;
	}
}

.offer_block__item_link_icon {
	position: absolute;
	top: 50%;
	right: 0;

	width: 1.2rem;
	height: 1.2rem;

	transform: translateY(-45%);
}
