@import 'ScssHelpers/helpers-all.scss';

.workspace_wix_tabs__nav--main_mod {
	@include bp($point_2, min) {
		@include flex_block(row, wrap, center, center);
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 6.6rem;
		}

		@include bp($point_3) {
			margin: 0 calc(var(--gl-indent) * -1) 4.5rem;
		}
	}
}

.workspace_wix_tabs__nav--sub_mod {
	@include bp($point_2, min) {
		flex: 0 1 50%;
	}
}

.workspace_wix_tabs__list {
	.workspace_wix_tabs__nav--main_mod & {
		@include bp($point_2, min) {
			margin: 0 -1.6rem -1.6rem 0;

			li {
				padding: 0 1.6rem 1.6rem 0;
			}

			@include flex_block(row, wrap, flex-start, center);
		}

		@include bp($point_3) {
			margin: 0 -2.2rem 0 0;

			li {
				padding: 0 2.2rem 0 0;
			}

			@include flex_block(row, nowrap, flex-start, center);
		}
	}
	.swiper-wrapper {
		margin: 0;
	}
}

.workspace_wix_tabs__nav--sub_mod .workspace_wix_tabs__item {
	&:not(:last-child) {
		.workspace_wix_tabs__link {
			&::before {
				height: 1px;

				background-color: $gray_2;
			}
		}
	}
}

.workspace_wix_tabs__item {
	.workspace_wix_tabs__nav--main_mod & {
		flex-shrink: 1;
		width: auto;

		text-align: center;
	}

	.workspace_wix_tabs__nav--sub_mod & {
		position: relative;

		@include bp($point_3) {
			&::before,
			&::after {
				content: '';
				position: absolute;
				right: 1.7rem;
				bottom: 50%;
				z-index: 3;

				width: 2.2rem;
				height: 0.2rem;

				background-color: $black_2;
				transform: translateY(50%);

				pointer-events: none;
				transition: transform 0.4s ease, background-color 0.4s ease,
					bottom 0.4s ease;
			}
			&::after {
				transform: translateY(50%) rotate(90deg);
			}

			&.workspace_wix_tab--active_state {
				&::before,
				&::after {
					bottom: 4.8rem;

					background-color: $white;
					transform: translateY(0);
				}
				&::after {
					transform: translateY(0) rotate(0deg);
				}
			}
		}

		&:not(.workspace_wix_tab--active_state) {
			color: $black_2;
		}

		&.workspace_wix_tab--active_state {
			color: $white;
		}
	}
}

.workspace_wix_tabs__link {
	.workspace_wix_tabs__nav--main_mod & {
		@include bp($point_2, min) {
			min-width: 19.8rem;
		}

		@include bp($point_3) {
			min-width: 16.8rem;
		}

		@include btn_style_transparent_2;
		font-weight: 700;

		@include btn_size_normal;

		&:focus {
			@include bp($point_2, min) {
				background-color: $black_2;
				color: $white;
			}
		}
	}
	.workspace_wix_tabs__nav--main_mod .workspace_wix_tab--active_state & {
		pointer-events: none;
		background-color: $black_2;
		color: $white;
	}

	.workspace_wix_tabs__nav--sub_mod & {
		position: relative;
		z-index: 1;

		display: block;

		cursor: pointer;
		transition: padding 0.4s ease;

		> * {
			position: relative;
			z-index: 3;
		}

		&::before,
		&::after {
			content: '';
			position: absolute;
			bottom: 0;

			@include bp($point_2, min) {
				right: 0;

				width: calc(100% + 9.8rem);
			}

			@include bp($point_3) {
				right: 50%;

				width: 150vw;

				transform: translateX(50%);
			}
		}

		&::before {
			z-index: 4;
		}

		&::after {
			transition: background-color 0.3s ease;
		}

		@include bp($point_2, min) {
			padding: 3.5rem 8.6rem 3.4rem 0;
		}

		@include bp($point_3) {
			padding: 4.1rem 0 4.2rem;
		}
	}

	.workspace_wix_tabs__nav--sub_mod
		.workspace_wix_tabs__item.workspace_wix_tab--active_state
		& {
		& > * {
			position: relative;
			z-index: 2;
		}

		&::after {
			height: 100%;

			background-color: $black_2;
		}

		@include bp($point_2, min) {
			padding-top: 3.9rem;
			padding-bottom: 1.7rem;
		}

		@include bp($point_3) {
			padding-top: 4.4rem;
			padding-bottom: 1.8rem;
		}
	}

	&:not(
			.workspace_wix_tabs__nav--sub_mod
				.workspace_wix_tabs__item.workspace_wix_tab--active_state
				&
		) {
		&::after {
			height: 100%;
			transition: background-color 0.4s ease;
		}
	}
}

.workspace_wix_tabs__nav_title {
	font-weight: 700;
	letter-spacing: 0.005em;

	transition: color 0.4s ease;

	@include bp($point_2, min) {
		font-size: 2.4rem;
	}

	@include bp($point_3) {
		padding-right: 5rem;

		font-size: 1.8rem;
	}
}

.workspace_wix_tabs__nav_content {
	a {
		@extend .workspace_wix_tabs__btn;
	}

	p {
		font-size: 1.6rem;
		line-height: 1.42;

		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 2.9rem;
			}

			@include bp($point_3) {
				margin-bottom: 3.1rem;
			}
		}
	}

	&:not(:first-child) {
		margin-top: 0;

		.workspace_wix_tabs__nav--sub_mod .workspace_wix_tab--active_state & {
			margin-top: 2.6rem;
		}
	}

	opacity: 0;
	visibility: hidden;
	overflow: hidden;
	transition: opacity 0.6s ease, visibility 0.4s ease, margin 0.4s ease,
		max-height 0.4s ease;
	will-change: opacity, margin, max-height;

	&:not(.workspace_wix_tabs__nav--sub_mod .workspace_wix_tab--active_state &) {
		max-height: 0;
		padding-bottom: 0;
	}

	.workspace_wix_tabs__nav--sub_mod .workspace_wix_tab--active_state & {
		opacity: 1;
		visibility: visible;

		@include bp($point_2, min) {
			padding-bottom: 3rem;
		}

		@include bp($point_3) {
			padding-bottom: 3rem;
		}
	}
}

.workspace_wix_tabs__description {
	font-size: 1.6rem;
	line-height: 1.42;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2.9rem;
		}

		@include bp($point_3) {
			margin-bottom: 3.1rem;
		}
	}
}

.workspace_wix_tabs__btn {
	min-width: 16.4rem;

	@include btn_style_green_3;

	@include btn_size_normal;
}
