@import 'ScssHelpers/helpers-all.scss';

.advice {
	color: $white;

	background-color: $black_2;

	@include bp($point_0 + 1, min) {
		padding: 8.3rem 8.9rem 8.5rem;
	}

	@include bp($point_0, $point_1) {
		padding: 8.3rem 13.263889% 8.5rem 8.9rem;
	}

	@include bp($point_1 - 1, $point_2) {
		padding: 8.3rem 8.9rem 8.5rem;
	}

	@include bp($point_3) {
		padding: 4.3rem 3.2rem 5.1rem 3.2rem;
	}
}

.advice__header {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 7.1rem;
		}

		@include bp($point_3) {
			margin-bottom: 4.6rem;
		}
	}
}

.advice__title {
	text-transform: uppercase;
	letter-spacing: 0.002em;
	@include h3;

	@include bp($point_1, $point_2) {
		font-size: 4rem;
	}

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 2.4rem;
		}
		line-height: 1.3;
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 3.8rem;
		}
		line-height: 1.1;
	}
}

.advice__subtitle {
	max-width: 59.5rem;

	font-size: 1.8rem;
	line-height: 1.43;
	letter-spacing: 0.021em;
}

.advice__row {
	@include bp($point_2, min) {
		@include flex_block(row, nowrap);
	}
}

.advice__col {
	@include bp($point_2, min) {
		&:nth-child(1) {
			flex: 0 0 46.38%;
		}
		&:nth-child(2) {
			flex: 1 1 auto;
			padding-left: 10.8rem;
		}
	}

	@include bp($point_3) {
		&:nth-child(1) {
			margin: 0 auto;

			max-width: 50rem;
		}
		&:not(:last-child) {
			margin-bottom: 4.3rem;
		}
	}
}

.advice_picture {
	position: relative;

	padding-bottom: 100.373%;
	display: block;
}

.advice__img {
	position: absolute;
	top: 0;
	left: 0;

	@include contain_img;
}

.advice__content {
	@include bp($point_2, min) {
		padding-top: 2.4rem;
	}
}

.advice__items {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 4.8rem;
		}

		@include bp($point_3) {
			margin-bottom: 4rem;
		}
	}
}

.advice__item {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 4.2rem;
		}

		@include bp($point_3) {
			margin-bottom: 3.9rem;
		}
	}
}

.advice__item_title {
	line-height: 1.16;

	@include h5;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1.9rem;
		}
		@include bp($point_3) {
			margin-bottom: 2rem;
		}
	}
}

.advice__description {
	@include description;

	@include bp($point_2, min) {
		line-height: 1.44;
	}

	@include bp($point_3) {
		line-height: 1.4;
	}
}

.advice__actions {
	@include bp($point_5, min) {
		margin: 0 -4.3rem -2.3rem 0;

		@include flex_block(row, wrap, flex-start, center);
	}

	@include bp($point_5 - 1) {
		margin: 0 -1.6rem -1.6rem 0;

		@include flex_block(column, nowrap, flex-start, center);
	}
}

.advice__action_item {
	@include bp($point_5, min) {
		padding: 0 4.3rem 2.3rem 0;
	}

	@include bp($point_5 - 1) {
		padding: 0 1.6rem 1.6rem 0;
	}
}

.advice__btn--mod_1 {
	min-width: 16.4rem;

	@include btn_style_green;

	@include btn_size_normal;
}

.advice__btn--mod_2 {
	display: inline-flex;
	align-items: center;
	height: 4.5rem;

	font-weight: 700;
	line-height: 4.5rem;
	font-size: 1.6rem;
	color: $white;

	transition: color 0.3s ease;

	&:hover {
		text-decoration: none;
		color: $green;
	}
}

.advice__icon-arrow {
	margin-left: 0.4rem;

	flex: 0 0 1.2rem;
	width: 1.2rem;
	height: 1rem;
}
