@import "ScssHelpers/helpers-all.scss";

.customer_requests {
	background-color: $black_2;
	@include bp($point_2, min) {
		padding: 7.1rem 3.3rem 8.9rem 3.3rem;
	}

	@include bp($point_3) {
		padding: 4.9rem 3.3rem 7.2rem 3.3rem;

		border-bottom: 1px solid rgba($gray, 0.2);
	}
}

.customer_requests_in {
	max-width: 116.8rem;
}

.customer_requests__title {
	text-align: center;
	color: $white;
	@include bp($point_2, min) {
		margin-bottom: 5.1rem;

		font-size: 4.8rem;
		line-height: 1.35;
		font-weight: 700;
	}

	@include bp($point_3) {
		margin-bottom: 4.6rem;

		font-size: 2.4rem;
		line-height: 1.25;
		letter-spacing: 0.03em;
	}
}

.customer_requests--light_mod {
	background-color: $white;
}

.customer_requests--light_mod .customer_requests__title {
	color: $black_2;
}


