@import 'ScssHelpers/helpers-all.scss';

.terms {
	background-color: #f5f7f7;

	@include bp($point_2, min) {
		padding: var(--height-header) 3.2rem 9.5rem 3.2rem;
	}
	@include bp($point_3) {
		padding: var(--height-header) 3.2rem 6.4rem 3.2rem;
	}
	@include bp($point_7) {
		padding: var(--height-header) 1.6rem 6.4rem 1.6rem;
	}
}

.terms_in {
	max-width: 105.3rem;
	width: 100%;
	margin: 0 auto;
}

.terms__header {
	position: relative;
	color: $white;

	&:after {
		content: '';
		position: absolute;
		top: 0;
		left: 50%;
		transform: translateX(-50%);

		width: 100vw;
		height: 100%;

		background-color: $black_2;
		pointer-events: none;
	}

	@include bp($point_2, min) {
		padding: 5.2rem 0 15.8rem 0;
	}

	@include bp($point_3) {
		padding: 3.6rem 0 15.8rem 0;
	}
}

.terms__top {
	position: relative;
	z-index: 1;

	@include bp($point_1 - 1) {
		text-align: center;
	}
}

.terms__body {
	position: relative;
	z-index: 1;
	@include bp($point_2, min) {
		margin-top: -10.2rem;
	}

	@include bp($point_3) {
		margin-top: -11.8rem;
	}
}

.terms__content {
	background-color: $white;

	@include bp($point_2, min) {
		padding: 8.4rem 14rem 12.1rem;
	}
	@include bp($point_3) {
		padding: 3.5rem 2.6rem 5.4rem;
	}

	h1 {
		@include h1;
	}
	h2 {
		@include h2;
	}

	h3 {
		font-weight: 400;

		@include bp($point_2, min) {
			margin-bottom: 2.6rem;

			font-size: 2.4rem;
		}
		@include bp($point_3) {
			margin-bottom: 2.2rem;

			font-size: 1.6rem;
			line-height: 1.7;
		}
	}

	h4 {
		margin-bottom: 2.7rem;

		font-weight: 400;
		text-transform: uppercase;

		@include bp($point_2, min) {
			font-size: 1.8rem;
		}

		@include bp($point_3) {
			font-size: 1.6rem;
		}
	}

	h5 {
		@include h5;
	}
	h6 {
		@include h6;
	}

	p {
		display: block;

		@include bp($point_2, min) {
			font-size: 1.8rem;
			line-height: 1.45;

			&:not(:last-child) {
				margin-bottom: 2.44rem;
			}
		}

		@include bp($point_3) {
			font-size: 1.6rem;
			line-height: 1.47;

			&:not(:last-child) {
				margin-bottom: 2rem;
			}
		}
	}

	ol,
	li {
		list-style: unset;
	}

	ol {
		margin-bottom: 2.2rem;

		list-style-type: upper-roman;
	}

	li {
		display: list-item;

		line-height: 1.45;

		padding-left: 0.5rem;
		margin-left: 3rem;

		&:not(:last-child) {
			margin-bottom: 2.5rem;
		}

		@include bp($point_2, min) {
			font-size: 1.8rem;
		}

		@include bp($point_3) {
			font-size: 1.6rem;
		}
	}
}

.terms__title {
	@include bp($point_2, min) {
		@include h3;
		line-height: 1.1;
	}

	@include bp($point_3) {
		margin-bottom: 3.4rem;

		font-size: 3rem;
		line-height: 1.3;
	}
}

.terms__date {
	font-size: 1.8rem;

	@include bp($point_2, min) {
		line-height: 2.6;
	}

	@include bp($point_3) {
		line-height: 1;
	}
}
