.customer_stories {
	margin-top: calc(var(--height-header) - var(--height-header) * 2);
	padding-left: 2.2rem;
	padding-right: 2.2rem;
	padding-top: calc(var(--height-header) + 11.5rem);

	@include bp($point_2, min) {
		min-height: 80rem;
		padding-bottom: calc(var(--height-header) + 1rem);

		@include flex_block(column, nowrap, flex-start);
	}

	@include bp($point_3) {
		min-height: 78.4rem;

		padding-bottom: 7.7rem;

		text-align: center;

		@include flex_block(column, nowrap, flex-start);
	}
}

.customer_stories__section_bg {
	background-color: $black_2;

	@include stretch;

	img {
		@include stretch;
		@include cover_img;

		object-position: 50% 0;

		mix-blend-mode: overlay;
	}
}

.customer_stories_in {
	margin: 0 auto;

	width: 100%;
	max-width: 122rem;
	@include flex_block(row, nowrap, flex-start);

	@include bp($point_2, min) {
		margin: auto;
	}
	@include bp($point_3) {
		margin-top: auto;
	}
}

.customer_stories__body {
	position: relative;
	z-index: 2;

	color: $white;

	@include bp($point_2, min) {
		margin-left: auto;

		flex: 0 1 50%;
	}

	@include bp($point_3) {
		text-align: center;
	}
}

.customer_stories__content {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 3.5rem;
		}

		@include bp($point_3) {
			margin-bottom: 2.7rem;
		}
	}
}

.customer_stories__title {
	text-transform: uppercase;
	letter-spacing: 0.013em;

	@include h3;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 3.2rem;
		}
	}

	@include bp($point_3) {
		line-height: 1.1;
		&:not(:last-child) {
			margin-bottom: 4rem;
		}
	}
}

.customer_stories__description {
	font-size: 1.8rem;

	@include bp($point_2, min) {
		line-height: 1.4;
		&:not(:last-child) {
			margin-bottom: 3.1rem;
		}
	}

	@include bp($point_3) {
		line-height: 1.44;
		letter-spacing: 0.02em;
		&:not(:last-child) {
			margin-bottom: 5.8rem;
		}
	}
}

.customer_stories__question_text {
	font-weight: 700;
	font-size: 1.8rem;
}

.customer_stories__btn {
	@include btn_style_orange_3;

	@include bp($point_2, min) {
		min-width: 16.4rem;

		@include btn_size_normal_2;
	}

	@include bp($point_3) {
		min-width: 17rem;

		@include btn_size_normal;
	}
}
