@import 'ScssHelpers/helpers-all.scss';

.about_us {
	background-color: #f5f7f7;

	@include bp($point_2, min) {
		padding: var(--height-header) 3.2rem 0 3.2rem;
	}
	@include bp($point_3) {
		padding: var(--height-header) 3.2rem 0 3.2rem;
	}
}

.about_us__in {
	max-width: 105.3rem;
}

.about_us__header {
	position: relative;
	color: $white;

	&:after {
		content: '';
		position: absolute;
		top: calc(var(--height-header) * -1);
		left: 50%;
		transform: translateX(-50%);

		width: 100vw;
		height: calc(var(--height-header) + 100%);

		background-color: $black_2;
		pointer-events: none;
	}

	@include bp($point_2, min) {
		padding: 4.3rem 0 15.8rem 0;
	}

	@include bp($point_3) {
		padding: 2rem 0 12.5rem 0;
	}
}

.about_us__top {
	position: relative;
	z-index: 1;
}

.about_us__body {
	position: relative;
	z-index: 1;
	@include bp($point_2, min) {
		margin-top: -9rem;
	}

	@include bp($point_3) {
		margin-top: -6.7rem;
	}
}

.about_us__title {
	color: $white;
	@include h1;

	@include bp($point_2, min) {
		max-width: 90rem;

		font-size: 5rem;
		line-height: 1.12;
	}

	@include bp($point_3, $point_4) {
		max-width: 56rem;

		line-height: 1.12;
	}

	@include bp($point_3) {
		max-width: 56rem;

		line-height: 1.12;
	}

	@include bp($point_4) {
		max-width: 99%;

		font-size: 3.5rem;
		line-height: 1.143;
	}
	@include bp($point_7) {
		font-size: 3rem;
	}
}

.about_us__list_wrapper {
	background-color: $white;
}

.about_us__list {
	@include bp($point_5) {
		padding-top: 0.5rem;
	}
}

.about_us__list_item {
	&:not(:last-child) {
		border-bottom: 1px solid $gray;
	}

	@include bp($point_1, min) {
		padding: 4rem 11rem 5rem 10.6rem;
	}
	@include bp($point_1, $point_2) {
		padding: 4rem 6rem 5rem;
	}
	@include bp($point_2, $point_5) {
		padding: 4rem 5rem 5rem;
	}
	@include bp($point_5) {
		padding: 2.4rem 2.4rem 3.8rem 2.4rem;
	}
}

.about_us__list_item_title {
	color: $black_2;
	@include h2;

	@include bp($point_5, min) {
		margin-bottom: 4rem;

		font-size: 4rem;
		line-height: 1.175;
	}
	@include bp($point_5) {
		margin-bottom: 2.6rem;

		line-height: 1.567;
	}
	@include bp($point_7) {
		font-size: 2.8rem;
	}
}

.about_us__list_item_content {
	@include bp($point_2, min) {
		padding-left: 10.8rem;
	}
	@include bp($point_3, $point_4) {
		padding-left: 6rem;
	}
	@include bp($point_4) {
		padding-left: 0;
	}
	& > p {
		color: $black_2;
		font-weight: 400;
		letter-spacing: 0;

		@include text();

		@include bp($point_5, min) {
			font-size: 1.8rem;
			line-height: 1.42;
			& > strong {
				font-size: 2.1rem;
				line-height: 1.33;
			}
			&:not(:last-child) {
				margin-bottom: 2.4rem;
			}
		}
		@include bp($point_5) {
			line-height: 1.42;
			& > strong {
				font-size: 1.6rem;
				line-height: 1;
			}
			&:not(:last-child) {
				margin-bottom: 3rem;
			}
		}
	}
}

.about_us__team_list {
	& > li {
		&:not(:first-child) {
			margin-top: 3rem;
		}
	}
	@include bp($point_5, min) {
		padding-top: 2.4rem;
	}
	@include bp($point_5) {
		padding-top: 1.2rem;
	}
}
