@import 'ScssHelpers/helpers-all.scss';

.create_design {
	@include bp($point_2, min) {
		padding: 5rem 0 10.6rem;
	}
	@include bp($point_3) {
		padding: 5rem 0;
	}

	@include bp($point_1 - 1) {
		&:after {
			position: absolute;
			top: 0;
			left: 0;
			z-index: -1;
			width: 100%;
			height: 19.6vw;
			content: '';

			background-color: $black_2;
		}
	}
}

.create_design__in {
	margin: 0 auto;
	padding: 0 var(--gl-indent);

	max-width: 130rem;
}

.create_design__img_w {
	display: block;
	max-width: 144rem;
	width: 100%;

	@include bp($point_1, min) {
		margin: -22.5rem auto 9.8rem;
	}
	@include bp($point_1 - 1) {
		margin: -5rem auto 4rem;
	}

	@include bp($point_2, min) {
		height: 51.8rem;
	}
	@include bp($point_3) {
		height: 36vw;
	}
}

.create_design__img {
	@include contain_img;
}

.create_design__row {
	@include bp($point_2, min) {
		@include flex_block(row, wrap, flex-start, stretch, stretch);
	}
}

.create_design__col {
	@include bp($point_2, min) {
		width: 50%;

		&:last-child {
			padding-left: 5.5rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 4.3rem;
		}
	}
}

.create_design__title {
	@include h3;

	line-height: 1.1;

	@include bp($point_2, min) {
		margin-top: 0.4rem;
		max-width: 47rem;
	}
}

.create_design__descr {
	@include description;

	line-height: 1.42;

	@include bp($point_2, min) {
		max-width: 50rem;
	}

	p:not(:last-child) {
		padding-bottom: 1.5rem;
	}
}
