.footer {
	flex: 0 0 auto;
	width: 100%;
	min-height: var(--height-footer);

	font-size: 1rem;

	background-color: $black_2;

	will-change: transform;

	@include bp($point_2, min) {
		padding: 2.5rem 3.8rem 1.4rem 2.9rem;
	}

	@include bp($point_3) {
		padding: 0 3.9rem 3rem 3.6rem;
	}
}

.footer__top {
	max-width: 152.5rem;
	width: 100%;
	margin: 0 auto;
	position: relative;

	display: flex;

	@include bp($point_2, min) {
		align-items: center;
		padding: 0 0 3rem;
	}

	@include bp($point_3) {
		flex-direction: column;
		padding: 3rem 0 4.3rem;
	}

	&::before {
		content: '';
		position: absolute;
		bottom: 0;
		left: 0;

		width: 200vw;
		height: 1px;

		background-color: $gray;
		transform: translateX(-50%);
		opacity: 20%;
	}
}

.footer__wrap {
	@include bp($point_3) {
		display: flex;
		justify-content: space-between;
		width: 100%;
		margin-bottom: 3.3rem;
	}
}

.footer__logo {
	display: block;
	flex-shrink: 0;
	margin-right: 5rem;

	@include bp($point_2, min) {
		width: 19.4rem;
		height: 5rem;
	}

	@include bp($point_3) {
		width: 5.4rem;
		height: 6.1rem;
	}
}

.footer__logo_img {
	@include contain_img;

	&--mobile_mod {
		@include bp($point_2, min) {
			display: none;
		}
	}

	&--desktop_mod {
		@include bp($point_3) {
			display: none;
		}
	}
}

.footer__menu {
	display: flex;
	flex-wrap: wrap;

	@include bp($point_2, min) {
		margin-left: auto;
	}

	@include bp($point_3) {
		margin-right: -3.5rem;
		margin-bottom: -1.9rem;
		padding-bottom: 2.2rem;
	}
}

.footer__menu_item {
	@include bp($point_1, min) {
		&:not(:last-child) {
			margin-right: 6rem;
		}
	}

	@include bp($point_1, $point_2) {
		&:not(:last-child) {
			margin-right: 3rem;
		}
	}

	@include bp($point_3) {
		flex: 0 0 50%;
		padding-right: 3.5rem;
		padding-bottom: 1.9rem;
	}
}

.footer__menu_link {
	line-height: 1.78;
	color: $gray;

	transition: color 0.3s ease 0s;

	@include bp($point_2, min) {
		font-size: 1.4rem;
		letter-spacing: 0.039em;
	}

	@include bp($point_3) {
		font-size: 1.6rem;
		letter-spacing: 0.02em;
	}

	&:hover,
	&:focus {
		color: $color_link_base;
		text-decoration: none;
	}
}

.footer__bottom {
	@include bp($point_2, min) {
		max-width: 152.5rem;
		width: 100%;
		margin: 0 auto;
		display: grid;
		align-items: center;

		gap: 2rem;
		grid-template-columns: 30% 1fr 30%;
	}

	@include bp($point_2, min) {
		padding: 0.8rem 0 0;
	}

	@include bp($point_3) {
		padding: 2.8rem 0 0;
	}
}

.footer__copy {
	color: $gray_3;

	@include bp($point_2, min) {
		font-size: 1.2rem;
		letter-spacing: 0.033rem;
	}

	@include bp($point_3) {
		font-size: 1.4rem;
		letter-spacing: 0.04rem;
	}
}

.footer__links_list {
	display: flex;
	flex-wrap: wrap;

	@include bp($point_2, min) {
		justify-content: flex-end;
	}

	&--desktop_mod {
		@include bp($point_3) {
			display: none;
		}
	}

	&--mobile_mod {
		@include bp($point_2, min) {
			display: none;
		}
	}
}

.footer__links_item {
	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-right: 4rem;
		}
	}

	@include bp($point_3) {
		flex: 0 0 50%;
		padding-right: 2rem;

		&:nth-child(even) {
			padding-right: 0;
			padding-left: 1.7rem;
		}
	}
}

.footer__link {
	text-align: left;
	color: $gray_3;

	transition: color 0.3s ease 0s;

	@include bp($point_2, min) {
		font-size: 1.2rem;
		line-height: 3.33;
		letter-spacing: 0.033rem;
	}

	@include bp($point_3) {
		font-size: 1.6rem;
		letter-spacing: 0.033rem;
	}

	&:hover,
	&:focus {
		color: $color_link_base;
		text-decoration: none;
	}
}

.footer__social {
	display: flex;
	justify-content: center;
	align-items: center;

	@include bp($point_2, min) {
		flex: 1 0 auto;
	}

	&--mobile_mod {
		@include bp($point_2, min) {
			display: none;
		}
	}

	&--desktop_mod {
		padding-left: 15px;

		@include bp($point_3) {
			display: none;
		}
	}
}

.footer__social_item {
	flex-shrink: 0;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-right: 2.8rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-right: 2.5rem;
		}
	}
}

.footer__social_link {
	svg {
		color: $gray_3;
		transition: color 0.3s ease 0s;

		&:hover,
		&:focus {
			color: $color_link_base;
		}

		@include bp($point_2, min) {
			max-width: 1.6rem;
			max-height: 1.8rem;
		}

		@include bp($point_3) {
			max-width: 2rem;
			max-height: 2rem;
		}
	}
}
