.hero_workspace {
	background-color: $black_2;

	@include bp($point_1, min) {
		padding: calc(9.4rem + var(--height-header)) 3.2rem 13.2rem;
	}

	@include bp($point_1 - 1) {
		padding: calc(3.7rem + var(--height-header)) 3.2rem 5.4rem;
	}
}

.hero_workspace__in {
	max-width: 123.2rem;
}

.hero_workspace__row {
	@include bp($point_2, min) {
		display: flex;
	}
}

.hero_workspace__col {
	@include bp($point_2, min) {
		flex-shrink: 0;
	}
	@include bp($point_3) {
		width: 100%;
	}

	&:not(:last-child) {
		@include bp($point_3) {
			margin-bottom: 4.6rem;
		}
	}

	&:nth-child(2n + 1) {
		@include bp($point_2, min) {
			position: relative;
			width: 42.9%;

			&::after {
				content: '';
				display: block;
				padding-bottom: 91%;
			}
		}
	}

	&:nth-child(2n + 2) {
		@include bp($point_2, min) {
			width: 57.1%;
			padding-top: 3rem;
			padding-left: 8.8rem;
		}
	}
}

.hero_workspace__title {
	@include h2;
	color: $white;

	text-transform: uppercase;
	letter-spacing: 0.004em;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 3.8rem;
		}

		@include bp($point_3) {
			margin-bottom: 3.4rem;
		}
	}

	@include bp($point_2, min) {
		line-height: 1.18;
	}
	@include bp($point_3) {
		line-height: 1.13;
	}
}

.hero_workspace__text {
	font-size: 1.8rem;
	line-height: 1.42;
	color: $white;
	font-weight: 400;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 4.1rem;
		}

		@include bp($point_3) {
			margin-bottom: 4.3rem;
		}
	}

	@include bp($point_3) {
		letter-spacing: 0.02em;
	}
}

.hero_workspace__btn_in {
	@include bp($point_3) {
		text-align: center;
	}
}

.hero_workspace__btn_in {
	@include bp($point_2, min) {
		margin-left: 0.5rem;
	}
}

.hero_workspace__btn {
	@include btn_style_orange_3;
	@include btn_size_normal;

	@include bp($point_2, min) {
		min-width: 16.4rem;
	}

	@include bp($point_3) {
		min-width: 15.6rem;
	}
}

.hero_workspace__pic {
	display: block;
	width: 100%;
	height: 100%;

	@include bp($point_2, min) {
		position: absolute;
		top: 0;
		left: 0;
	}
}

.hero_workspace__img {
	@include cover_img;
}
