@import 'ScssHelpers/helpers-all.scss';

.plans_card_mob__card {
	position: relative;
	z-index: 5;

	flex-grow: 1;

	background-color: $white;
	//border-bottom: 1px solid $gray;

	&:not(:last-child) {
		border-right: 1px solid $gray;
	}

	.pricing_tabs__tab_content[id='tab-0'] & {
		flex: 1 0 50%;
	}

	.pricing_tabs__tab_content[id='tab-1'] & {
		flex: 1 0 33%;
	}

	@include bp($point_3) {
		&:after {
			position: absolute;
			bottom: 0;
			left: 0;

			display: block;
			width: 100%;
			height: 1px;
			background-color: $gray;

			content: '';
		}
	}
}

.plans_card_mob__card_in {
	text-align: center;

	padding: 3rem 2rem 3.4rem;
}

.plans_card_mob__title {
	font-size: 1.8rem;
	font-weight: 700;

	&:not(:last-child) {
		margin-bottom: 1rem;
	}
}

.plans_card_mob__type {
	&:not(:last-child) {
		margin-bottom: 1.6rem;
	}
}

.plans_card_mob__price {
	font-size: 3rem;
	font-weight: 700;

	&:after {
		content: attr(data-price);
	}

	.card_product--active_mod & {
		&:after {
			content: attr(data-switch-price);
		}
	}
}

.plans_card_mob__plan {
	font-size: 1.8rem;
}

.plans_card_mob__btn {
	@include btn_style_black;
	@include btn_size_normal;
}
