@import "ScssHelpers/helpers-all.scss";

.customer_content {

	@include bp($point_2, min) {
		padding: 0 3.3rem 7.8rem;
	}

	@include bp($point_3) {
		padding: 0 3.3rem 5.3rem;
	}
}

.customer_content_in {
	max-width: 83.7rem;
	width: 100%;
	margin: 0 auto;
}

.customer_content__body {

	@include bp($point_2, min) {
		font-size: 1.8rem;
	}

	@include bp($point_3) {
		font-size: 1.6rem;
	}

	h1 {
		@include h1;
	}

	h2 {
		@include h2;
	}

	h3 {
		@include h3;
	}

	h4 {
		font-weight: 700;

		@include bp($point_2, min) {
			margin-bottom: 3.2rem;

			font-size: 3rem;
			line-height: 1.4;
		}

		@include bp($point_3) {
			margin-bottom: 3.1rem;

			font-size: 2.4rem;
			line-height: 1.26;
		}
	}

	h5 {
		@include h5;
	}

	h6 {
		@include h6;
	}

	p, blockquote {
		@include bp($point_2, min) {
			line-height: 1.38;

			&:not(:last-child) {
				margin-bottom: 3rem;
			}
		}

		@include bp($point_3) {
			line-height: 1.39;

			&:not(:last-child) {
				margin-bottom: 3rem;
			}
		}
	}

	a {
		font-weight: 700;

		transition: color 0.3s ease;

		&:hover {
			text-decoration: none;
			color: $black_2;
		}
	}

	strong {
		font-weight: 700;

		&:before {
			content: '';
			position: relative;
			top: 1px;

			margin-right: 1rem;

			font-size: 4rem;
			font-weight: 400;

			@include bp($point_2, min) {
				line-height: 0.3;
			}

			@include bp($point_3) {
				line-height: 0.4;
			}
		}
	}
}

.customer_content__picture {
	display: block;
	font-size: 0;
	line-height: 0;
}

.customer_content__img {
	width: 100%;
}

.wp-block-video {
	position: relative;
	display: inline-block;

	width: 100%;
	height: 100%;
	background: #000;

	@include bp($point_2, min) {
		margin-bottom: 2.5rem;
	}

	@include bp($point_3) {
		margin-bottom: 2.8rem;
		margin-top: 2rem;
	}

	span {
		position: absolute;
		top: unset;

		margin-right: 0;

		line-height: 1.3;
		font-size: 1.3rem;
	}
}

.customer_content__icon {
	position: absolute;
	top: 50%;
	left: 50%;

	width: 7.5rem;
	height: 7.5rem;

	transform: translate(-50%, -50%);

	cursor: pointer;
}


.wp-block-video .plyr--full-ui.plyr--video .plyr__control--overlaid {
	@include flex_block(row, wrap, center, center, center);

	border: 1px solid $white;

	background-color: $black_2;
	opacity: 1;

	width: 7.5rem;
	height: 7.5rem;

	&:hover {
		background-color: $white;
		color: $black_2;

		border-color: $black;
	}
}

.wp-block-video .plyr__poster {
	background-size: cover;
}

.wp-block-video .plyr__control--overlaid svg {
	width: 1.4rem;
	height: 1.8rem;
}

.wp-block-video .plyr__menu__container .plyr__control > span,
.wp-block-video .plyr__menu__container .plyr__menu__value, {
	line-height: 1;
	margin-right: 0;
	font-size: 1rem;
}
.wp-block-video .plyr__control[data-plyr=settings],
.wp-block-video .plyr__control[data-plyr=pip] {
	display: none;
}

.wp-block-video .plyr__volume input[type=range] {
	max-width: 5rem;
}