@import 'ScssHelpers/helpers-all.scss';

.workspace_list {
	@include bp($point_2, min) {
		padding: 5.6rem 10.2rem 5.4rem;
	}

	@include bp($point_3) {
		padding: 5rem var(--gl-indent);
	}

	&.workspace_list--reverse_mod:before {
		z-index: -1;
		position: absolute;
		top: 0;
		left: 0;

		width: 100vw;
		height: 100%;
		background-color: $gray;

		content: '';
	}
}

.workspace_list__in {
	max-width: 123.6rem;
	width: 100%;
}

.workspace_list__item {
	position: relative;

	@include bp($point_2, min) {
		&:not(.workspace_list--reverse_mod &) {
			margin-right: -7.4rem;
		}

		//.workspace_list--reverse_mod & {
		//	padding: 8rem 0 10.4rem;
		//}
	}

	//@include bp($point_3) {
	//	&:nth-child(odd) {
	//		padding: 4.2rem 0 4.4rem;
	//	}
	//
	//	&:nth-child(even) {
	//		padding: 5.2rem 0 5.4rem;
	//	}
	//}
}

.workspace_list__row {
	@include bp($point_2, min) {
		&:not(.workspace_list--reverse_mod &) {
			@include flex_block(row-reverse, wrap, flex-start, center);
		}

		.workspace_list--reverse_mod & {
			@include flex_block(row, wrap, flex-start, center);
		}
	}
}

.workspace_list__col_content {
	@include bp($point_2, min) {
		&:not(.workspace_list--reverse_mod &) {
			width: 43.2%;
			padding-right: 6.4rem;
			padding-bottom: 2rem;
		}

		.workspace_list--reverse_mod & {
			width: 57.4%;
			padding-top: 2.2rem;
			padding-left: 14.6rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 4.4rem;
		}
	}
}

.workspace_list__col_picture {
	@include bp($point_2, min) {
		&:not(.workspace_list--reverse_mod &) {
			width: 56.8%;
		}

		.workspace_list--reverse_mod & {
			width: 42.6%;
		}
	}

	@include bp($point_3) {
		&:not(.workspace_list--reverse_mod &) {
			margin: 0 -2rem 3.6rem;
		}

		.workspace_list--reverse_mod & {
			margin: 0 0 4.8rem;
		}
	}
}

.workspace_list__content {
	@include bp($point_2, min) {
		max-width: 50rem;
	}
}

.workspace_list__title {
	font-size: 3rem;

	color: $black_2;
	text-transform: uppercase;

	@include bp($point_2, min) {
		line-height: 1.4;
		letter-spacing: 0.018em;

		.workspace_list--big_title_mod & {
			font-size: 5rem;
			line-height: 1.1;

			&:not(:last-child) {
				margin-bottom: 3.4rem;
			}
		}

		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}

	@include bp($point_3) {
		line-height: 1.1;

		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}
}

.workspace_list__descrip {
	@include description;
	line-height: 1.45;
	color: $black_2;

	p:not(:last-child) {
		margin-bottom: 2rem;
	}

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 5.6rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 4.8rem;
		}
	}
}

.workspace_list__btn_wrap {
	@include bp($point_3) {
		text-align: center;
	}
}

.workspace_list__btn {
	@include btn_size_normal;
	@include btn_style_green_3;

	@include bp($point_2, min) {
		font-size: 1.8rem;

		&:not(.workspace_list--reverse_mod &) {
			min-width: 27.7rem;
		}

		.workspace_list--reverse_mod & {
			min-width: 21.5rem;
		}
	}

	@include bp($point_3) {
		&:not(.workspace_list--reverse_mod &) {
			min-width: 23.4rem;
		}

		.workspace_list--reverse_mod & {
			min-width: 15.6rem;
		}
	}
}

.workspace_list__pic {
	display: block;
	width: 100%;

	@include bp($point_2, min) {
		height: 47.2rem;
	}

	@include bp($point_3) {
		height: 100%;

		&:not(:last-child) {
			margin-bottom: 6rem;
		}
	}
}

.workspace_list__img {
	@include contain_img;
}
