@import 'ScssHelpers/helpers-all.scss';

.contact_us {
	@include bp($point_2, min) {
		padding: calc(var(--height-header) + 7.3rem) var(--gl-indent) 10.2rem;
	}
	@include bp($point_3) {
		padding: calc(var(--height-header) + 4.7rem) var(--gl-indent) 6.8rem;
	}
}

.contact_us__in {
	margin: 0 auto;
	max-width: 104rem;
}

.contact_us__bg {
	position: absolute;
	top: 0;
	left: 0;
	z-index: -1;

	width: 100%;
	height: 100%;

	@include bp($point_3) {
		background-color: $gray;
	}
}

.contact_us__img_w {
	display: block;

	height: 100%;

	@include bp($point_3) {
		display: none;
	}
}

.contact_us__img {
	@include cover_img;
}

.contact_us__form_w {
	background-color: $white;
}

.contact_us__row {
	@include bp($point_2, min) {
		margin-bottom: 3.2rem;

		&.contact_us__row--offset_mod {
			margin-bottom: 3.7rem;
		}
	}
	@include bp($point_3) {
		margin-bottom: 2.4rem;
	}
}

.contact_us__row_text_area {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 3.6rem;
		}
		@include bp($point_3) {
			margin-bottom: 5.2rem;
		}
	}
}

.contact_us__row_in {
	@include flex_block(row, wrap, flex-start, stretch, stretch);

	margin: 0 -4rem -2.4rem 0;
}

.contact_us__col {
	padding: 0 4rem 2.4rem 0;

	@include bp($point_2, min) {
		width: 50%;
	}
	@include bp($point_3) {
		width: 100%;
	}
}

.contact_us__info {
	@include bp($point_2, min) {
		margin-bottom: 5.6rem;
	}
	@include bp($point_3) {
		margin-bottom: 4.1rem;
	}
}

.contact_us__title {
	text-transform: uppercase;
	font-weight: 700;

	@include bp($point_2, min) {
		font-size: 3.6rem;
		line-height: 1.47;

		&:not(:last-child) {
			margin-bottom: 1.7rem;
		}
	}
	@include bp($point_3) {
		font-size: 3rem;
		line-height: 1.33;

		&:not(:last-child) {
			margin-bottom: 4.3rem;
		}
	}
}

.contact_us__descr {
	line-height: 1.4;

	@include bp($point_2, min) {
		font-size: 2.4rem;
	}

	@include bp($point_3) {
		font-size: 1.8rem;
	}
}

.contact_us__form_in {
	@include bp($point_2, min) {
		padding: 4.8rem 7rem 5.7rem;
	}
	@include bp($point_3) {
		padding: 4.3rem 3rem 6rem;
	}
}

.form_textarea__field {
	position: relative;

	&:after {
		position: absolute;
		bottom: 0;
		right: 0;
		display: block;
		width: 0.9rem;
		height: 0.8rem;
		content: '';

		background: url(../../images/resize.svg) no-repeat center center / cover;
	}

	&:before {
		position: absolute;
		bottom: 0;
		right: 0;
		display: block;
		width: 0.9rem;
		height: 0.8rem;
		content: '';

		background-color: $white;
	}
}

.ts-wrapper {
	&:after {
		position: absolute;
		top: 1.6rem;
		right: 1.4rem;
		z-index: 1;

		width: 1.2rem;
		height: 1.2rem;
		content: '';

		background-image: url('../../images/select-arrow.svg');
		background-repeat: no-repeat;
		background-size: 100% 100%;
		pointer-events: none;

		transition: transform ease 0.3s;
	}

	&.dropdown-active::after {
		transform: rotate(180deg);
	}

	.ts-control {
		position: relative;
		padding: 0.9rem 1.3rem 0.8rem 1.3rem;
		padding-right: 4rem !important;
		border-top: none;
		border-left: none;
		border-right: none;

		&:after {
			position: absolute;
			top: 1.6rem;
			right: 1.4rem;
			z-index: 1;

			width: 1.2rem;
			height: 1.2rem;

			content: '';

			background-image: url('../../images/select-arrow.svg');
			background-repeat: no-repeat;
			background-size: 100% 100%;

			transition: transform ease 0.3s;
		}

		.item {
			font-size: 1.6rem;
		}

		input {
			font-size: 1.6rem;
		}

		& > div {
			font-weight: 700;
			color: $black_2;
		}
	}

	&.dropdown-active .ts-control::after {
		transform: rotate(180deg);
	}

	.ts-dropdown {
		padding-top: 1rem;
		padding-bottom: 1rem;
		border-radius: 0.6rem;
		border: none;

		box-shadow: 0 0 0.8rem 0.1rem rgba(19, 35, 39, 13%);

		div {
			color: $black_2;
		}
	}

	.ts-dropdown .option {
		padding: 1rem 1.6rem;
		font-size: 1.6rem;
		letter-spacing: 0.03em;
		margin: 0 0.6rem;

		&.active {
			// background: none;

			background-color: $gray_1;
			border-radius: 5px;
		}
	}
}

.has-items .ts-control > input {
	display: none !important;
}

.contact_us__btn_w {
	@include flex_block(row, wrap, center, center, center);
}

.contact_us__btn {
	min-width: 19rem;

	border: none;
	text-transform: uppercase;
	border: 1px solid $black_2;
}

.wpcf7 {
	.wpcf7-response-output {
		// display: none !important;
		border: none !important;
	}
}
