@import 'ScssHelpers/helpers-all.scss';

.create_website {
	background-color: $gray;

	&.create_website--bg_mod {
		background-color: $black_2;
	}

	@include bp($point_2, min) {
		padding: 6rem 10.4rem 6rem 0;

		&.create_website--style_mod {
			padding-left: 10.4rem;
			padding-right: 0;
		}

		&.create_website--bg_mod {
			padding-bottom: 10rem;
			padding-top: 10rem;
		}
	}

	@include bp($point_3) {
		padding: 3rem 3.2rem 2.5rem;

		&.create_website--bg_mod {
			padding-bottom: 5.5rem;
		}
	}

	&:last-child {
		@include bp($point_2, min) {
			padding-bottom: 12rem;
		}

		@include bp($point_3) {
			padding-bottom: 5.5rem;
		}
	}
}

.create_website__in {
	max-width: 133.6rem;
	width: 100%;
	margin: 0 auto;
}

.create_website__row {
	@include bp($point_2, min) {
		display: flex;
		align-items: center;
	}
}

.create_website__col {
	&:nth-child(2n + 1) {
		@include bp($point_2, min) {
			width: 57.6%;

			.create_website--style_mod & {
				order: 2;
			}
		}
	}

	&:nth-child(2n + 2) {
		@include bp($point_2, min) {
			.create_website--style_mod & {
				order: 1;
				margin-right: auto;
				padding-right: 3rem;
			}
			width: 42.4%;
			max-width: 53rem;

			&:not(.create_website--style_mod &) {
				margin-left: auto;
				padding-left: 3rem;
			}
		}
	}

	@include bp($point_2, min) {
		flex-shrink: 0;
	}

	&:not(:last-child) {
		@include bp($point_3) {
			margin-bottom: 5rem;
		}
	}
}

.create_website__pic {
	display: block;
	width: 100%;
}

.create_website__img {
	@include cover_img;
}

.create_website__title {
	font-size: 3rem;
	font-weight: 700;
	line-height: 1.42;
	text-transform: uppercase;

	&:not(.create_website--bg_mod &) {
		color: $black_2;
	}

	.create_website--bg_mod & {
		color: $white;
	}

	@include bp($point_2, min) {
		line-height: 1.4;
		letter-spacing: 0.003em;
	}

	@include bp($point_3) {
		line-height: 1.1;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2.2rem;
		}
		@include bp($point_3) {
			margin-bottom: 2.4rem;
		}
	}
}

.create_website__text {
	font-weight: 400;
	line-height: 1.42;

	&:not(.create_website--bg_mod &) {
		color: $black_2;
	}

	.create_website--bg_mod & {
		color: $white;
	}

	@include bp($point_2, min) {
		font-size: 1.8rem;
	}

	@include bp($point_3) {
		font-size: 1.6rem;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 5.8rem;
		}

		@include bp($point_3) {
			margin-bottom: 4.6rem;
		}
	}

	p:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2rem;
		}

		@include bp($point_3) {
			margin-bottom: 1.6rem;
		}
	}
}

.create_website__btn_in {
	@include bp($point_3) {
		text-align: center;
	}
}

.create_website__btn {
	@include btn_style_green_3;

	@include bp($point_2, min) {
		min-width: 21.5rem;
		@include btn_size_normal_2;
	}

	@include bp($point_3) {
		@include btn_size_normal;
	}
}
