.hero_features {
	margin-top: calc(var(--height-header) - var(--height-header) * 2);

	&:not(.hero_features--mod_1) {
		background-color: $black_2;

		@include bp($point_2, min) {
			padding: calc(var(--height-header) + 17.1rem) var(--gl-indent) 10.4rem
				10rem;
		}

		@include bp($point_3) {
			padding: calc(var(--height-header) + 14.5rem) 3.2rem 0;
		}
	}

	&.hero_features--mod_1 {
		background-color: $orange;
		overflow: hidden;

		@include bp($point_2, min) {
			padding: calc(var(--height-header) * 2) 9.2rem 0;
		}

		@include bp($point_3) {
			padding: calc(var(--height-header) + 11.5rem) 3.2rem 0;
		}
	}
}

.hero_features__row {
	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start, flex-start);
	}
}

.hero_features__in {
	width: 100%;
	margin: 0 auto;

	&:not(.hero_features--mod_1) {
		max-width: 130.8rem;
	}
	.hero_features--mod_1 & {
		max-width: 125.6rem;
	}
}

.hero_features__col_content {
	@include bp($point_10, min) {
		&:not(.hero_features--mod_1 &) {
			width: 68.3rem;
		}

		.hero_features--mod_1 & {
			padding: 6rem 6.5rem 6rem 0;
			flex: 0 0 69.4rem;
		}
	}

	@include bp($point_10 - 1, $point_2) {
		&:not(.hero_features--mod_1 &) {
			width: 52.2%;
		}

		.hero_features--mod_1 & {
			padding: 6rem 2rem 6rem 0;
			flex: 0 0 55.25%;
		}
	}

	@include bp($point_2, min) {
		&:not(.hero_features--mod_1 &) {
			padding-top: 1.2rem;
			padding-right: 3rem;
		}

		.hero_features--mod_1 & {
			line-height: 1.25;
			letter-spacing: 0.005em;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			&:not(.hero_features--mod_1 &) {
				margin-bottom: 6.4rem;
			}

			.hero_features--mod_1 & {
				margin-bottom: 5.3rem;
			}
		}
	}
}

.hero_features__col_pic {
	@include bp($point_10, min) {
		&:not(.hero_features--mod_1 &) {
			width: 62.5rem;
			flex: 1 1 62.5rem;
		}

		.hero_features--mod_1 & {
			flex: 1 1 56.2rem;
		}
	}

	@include bp($point_10 - 1, $point_2) {
		&:not(.hero_features--mod_1 &) {
			width: 47.8%;
		}

		.hero_features--mod_1 & {
			margin-right: -9.2rem;
			flex: 1 1 auto;
		}
	}

	@include bp($point_2, min) {
		.hero_features--mod_1 & {
			align-self: flex-end;
		}
	}

	@include bp($point_3) {
		.hero_features--mod_1 & {
			margin-right: -3.2rem;
			margin-left: -0.4rem;
		}
	}
}

.hero_features__title {
	text-transform: uppercase;
	color: $white;

	&:not(.hero_features--mod_1 &) {
		@include h4;

		@include bp($point_2, min) {
			line-height: 1.375;

			&:not(:last-child) {
				margin-bottom: 3.8rem;
			}
		}

		@include bp($point_3) {
			line-height: 1.25;
			letter-spacing: 0.03em;

			&:not(:last-child) {
				margin-bottom: 3.2rem;
			}
		}
	}

	.hero_features--mod_1 & {
		letter-spacing: 0.005em;

		@include h2;

		@include bp($point_2, min) {
			&:not(:last-child) {
				margin-bottom: 4rem;
			}
		}

		@include bp($point_3) {
			line-height: 1.1;
		}

		@include bp($point_1, $point_2) {
			font-size: 4.4rem;
		}

		@include bp($point_3, $point_5) {
			&:not(:last-child) {
				margin-bottom: 3.6rem;
			}

			font-size: 4rem;
		}

		@include bp($point_5 - 1) {
			&:not(:last-child) {
				margin-bottom: 3.4rem;
			}

			font-size: 3rem;
		}
	}
}

.hero_features__descrip {
	color: $white;

	&:not(.hero_features--mod_1 &) {
		font-weight: 400;
		line-height: 1.5;

		@include bp($point_2, min) {
			font-size: 1.8rem;

			&:not(:last-child) {
				margin-bottom: 4.6rem;
			}
		}

		@include bp($point_3) {
			font-size: 1.6rem;
			letter-spacing: 0.02em;

			&:not(:last-child) {
				margin-bottom: 4.2rem;
			}
		}
	}

	.hero_features--mod_1 & {
		@include bp($point_2, min) {
			&:not(:last-child) {
				margin-bottom: 4.8rem;
			}

			font-size: 2.3rem;
			line-height: 1.4;
			letter-spacing: 0.024em;
		}

		@include bp($point_3) {
			&:not(:last-child) {
				margin-bottom: 3.3rem;
			}

			font-size: 1.8rem;
			line-height: 1.42;
			letter-spacing: 0.021em;
		}
	}
}

.hero_features__bottom_wrap {
	@include bp($point_3) {
		display: flex;
		flex-direction: column-reverse;
	}
}

.hero_features__btn_list_wrap {
	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 5.2rem;
		}
	}
}

.hero_features__btn_list {
	@include flex_block(row, wrap, flex-start, center);

	&:not(.hero_features--mod_1 &) {
		@include bp($point_2, min) {
			margin: 0 -3rem -3rem 0;
		}

		@include bp($point_3) {
			margin: 0 -1.8rem -1.8rem 0;
		}
	}

	.hero_features--mod_1 & {
		@include bp($point_2, min) {
			margin: 0 -2.4rem -2.4rem 0;
		}

		@include bp($point_3) {
			margin: 0 -2.3rem -2.3rem -0.4rem;
		}
	}
}

.hero_features__btn_item {
	&:not(.hero_features--mod_1 &) {
		@include bp($point_2, min) {
			padding: 0 3rem 3rem 0;
		}

		@include bp($point_3) {
			padding: 0 1.8rem 1.8rem 0;
		}
	}

	.hero_features--mod_1 & {
		@include bp($point_2, min) {
			padding: 0 2.4rem 2.4rem 0;
		}

		@include bp($point_3) {
			padding: 0 2.3rem 2.3rem 0;
		}
	}
}

.hero_features__btn_link {
	font-weight: 700;

	&:not(.hero_features__btn_link--transparent_mod) {
		@include btn_size_normal;
	}

	&.hero_features__btn_link--primary_mod {
		@include btn_style_orange_3;
		//box-shadow: none;
	}

	&.hero_features__btn_link--outline_mod {
		@include btn_style_orange;
	}

	&.hero_features__btn_link--black_mod {
		min-width: 19.4rem;

		@include btn_style_white;

		border-color: transparent;
	}

	&.hero_features__btn_link--transparent_mod {
		display: inline-flex;
		align-items: center;
		height: 4.5rem;

		font-weight: 700;
		font-size: 1.6rem;
		line-height: 4.5rem;
		color: $black_2;
		white-space: nowrap;

		transition: color 0.3s ease;

		span.hero_btn__icon {
			margin-right: 1rem;

			@include bp($point_2, min) {
				flex: 0 0 2.5rem;
				width: 2.5rem;
				height: 2.5rem;
			}

			@include bp($point_3) {
				flex: 0 0 2rem;
				width: 2rem;
				height: 2rem;
			}
		}

		&:hover,
		&:focus {
			color: $white;
			text-decoration: none;
		}
	}

	&:not(.hero_features--mod_1 &) {
		@include bp($point_2, min) {
			min-width: 19rem;
			font-size: 1.8rem;
		}

		@include bp($point_3) {
			min-width: 16.4rem;
		}
	}
}

.hero_features__bottom_list {
	@include flex_block(row, wrap, flex-start, center);

	@include bp($point_2, min) {
		margin: 0 -2.6rem -2.6rem 0;
	}

	@include bp($point_3) {
		margin: 0 -2.6rem 2.6rem 0;
	}
}

.hero_features__bottom_item {
	@include flex_block(row, wrap, flex-start, flex-start, flex-start);
	padding: 0 2.6rem 2.6rem 0;
}

.hero_features__bottom_icon {
	width: 1.6rem;
	height: 1.6rem;
	flex-shrink: 0;

	color: $orange;

	&:not(:last-child) {
		margin-right: 0.8em;
	}
}

.hero_features__bottom_text {
	font-size: 1.4rem;
	font-weight: 400;
	line-height: 1.7rem;
	letter-spacing: 0.03em;

	color: $white;
}

.hero_features__pic {
	position: relative;

	display: block;

	&:not(.hero_features--mod_1 &) {
		@include bp($point_10, min) {
			--col-content-width: 68.3rem;
			--col-picture-width: 62.5rem;
			--section-in-width: 130.8rem;

			height: 49.6rem;
			margin-right: calc(
				((100vw - var(--section-in-width)) / 2 * -1) -
					(
						var(--section-in-width) -
							(var(--col-picture-width) + var(--col-content-width)) -
							var(--gl-indent)
					)
			);
		}

		@include bp($point_10 - 1) {
			margin-right: calc(var(--gl-indent) * -1);
		}

		@include bp($point_3) {
			padding-top: 80%;
			margin-right: -3.7rem;
		}
	}

	.hero_features--mod_1 & {
		margin-bottom: -1px;

		@include bp($point_2, min) {
			height: 58.3rem;
		}

		@include bp($point_2, min) {
			min-width: 115.9rem;
			--col-content-width: 69.4rem;
			--col-picture-width: 56.2rem;
			--section-in-width: 125.6rem;

			margin-right: calc(
				((100vw - var(--section-in-width)) / 2 * -1) -
					(
						var(--section-in-width) -
							(var(--col-picture-width) + var(--col-content-width))
					)
			);
		}

		@include bp($point_3, $point_4) {
			padding-bottom: 60.33%;
		}

		@include bp($point_4 - 1) {
			padding-bottom: 92.23%;
		}
	}
}

.hero_features__img {
	object-position: 0 100%;

	@include contain_img;

	@include bp($point_2, min) {
		.hero_features--mod_1 & {
			object-position: 0 100%;
		}
	}

	@include bp($point_3) {
		@include stretch;
	}
}
