@import 'ScssHelpers/helpers-all.scss';

.card_product__body {
	@include bp($point_2, min) {
		display: flex;
		flex-wrap: wrap;

		margin: 0 -1.9rem -1.9rem 0;
	}
}

.card_product__wrapper {
	position: relative;
	height: 100%;

	text-align: center;

	border: 1px solid $gray;

	transition: background-color 1s ease-in-out, color 0.6s ease-in-out;

	padding: 2.2rem 1.7rem 3.8rem;

	@include bp($point_3) {
		padding-top: 3.8rem;
	}

	.card_product__card--v2_mod & {
		padding: 0;
		border: none;

		@include bp($point_3) {
			padding: 1rem 0 1rem;
		}
	}
}

.card_product__card {
	height: auto;

	@include bp($point_2, min) {
		flex: 0 0 25%;

		width: 100% !important;
		margin-right: 0 !important;

		padding: 0 1.9rem 1.9rem 0;
	}

	&:hover {
		.card_product__wrapper {
			color: $white;
			background-color: $black_2;

			.card_product__btn {
				@include btn_style_white;
				@include btn_size_normal;

				transition: background-color 1s ease-in-out, color 0.3s ease-in-out,
					border-color 0.3s ease-in-out;
			}
		}
	}

	&.card_product__card--v2_mod {
		flex: 1 0 33.333%;
		z-index: 99;
		background-color: $white;

		@include bp($point_2, min) {
			padding: 0;
		}

		@include bp($point_3) {
			border: 1px solid $gray;

			&:not(:last-child) {
				margin-bottom: 2.6rem;
			}
		}

		&:hover {
			.card_product__wrapper {
				color: $black_2;
				background-color: unset;
			}

			.card_product__btn {
				@include btn_style_black;
				@include btn_size_normal;
			}
		}
	}

	&.card_product__card--popular_mod {
		background-color: $gray_1;

		.card_product__btn {
			background-color: $gray_1;
		}
	}
}

.card_product__overlay {
	.card_product__card--v2_mod & {
		position: absolute;
		bottom: calc(100% + 1px);
		left: -2px;

		width: 101%;
		height: 7rem;

		// opacity: 0;

		background-color: $white;
		pointer-events: none;

		@include bp($point_3) {
			display: none;
		}
	}
}

.card_product__top {
	z-index: 10;

	.header--locked_state + .wrapper & {
		border-bottom: 1px solid $gray;
	}

	.primary_products .card_product__card:last-child & {
		border-right: none;
	}

	.card_product__card:last-child & {
		@include bp($point_2, min) {
			border-right: 1px solid $gray;
		}
	}

	&.card_product__top--scroll_mod {
		border-bottom: 1px solid $gray !important;
	}

	.card_product__card--v2_mod & {
		padding: 2.8rem 1.7rem 0.1rem;

		@include bp($point_2, min) {
			border-top: 1px solid $gray;
			border-left: 1px solid $gray;
			background-color: $white;
		}
	}

	.card_product__card--popular_mod & {
		background-color: $gray_1 !important;
	}
}

.card_product__bottom {
	.card_product__card--v2_mod:last-child &:after {
		@include bp($point_2, min) {
			border-right: 1px solid $gray;
			// border-right: 10px solid red;
		}
	}

	.card_product__card--v2_mod & {
		padding: 0 1.7rem 3.8rem;

		@include bp($point_2, min) {
			&:after {
				position: absolute;
				bottom: 0;
				left: 0;

				width: 100%;
				height: 100%;

				// border: 1px solid $gray;
				// border-top: 1px solid $gray;
				border-left: 1px solid $gray;
				border-bottom: 1px solid $gray;
				border-top: none;
				content: '';
				pointer-events: none;
			}
		}
	}
}

.card_product__label {
	@include flex_block(row, wrap, center, center, center);
	position: absolute;
	top: -2.1rem;
	left: 50%;

	width: 15.6rem;
	height: 3.2rem;

	background-color: $white;
	border-radius: 4rem;
	border: 1px solid $orange;

	transform: translateX(-50%);
}

.card_product__label_text {
	font-size: 1.2rem;
	font-weight: 700;
	text-transform: uppercase;
	color: $orange;
}

.card_product__title {
	font-size: 2.4rem;
	font-weight: 700;
	line-height: 1.37;

	@include bp($point_2, min) {
		margin-bottom: 3.1rem;
	}

	@include bp($point_3) {
		margin-bottom: 2rem;
	}
}

.card_product__type {
	@include bp($point_2, min) {
		margin-bottom: 3.2rem;
	}

	@include bp($point_3) {
		margin-bottom: 2rem;
	}
}

.card_product__plan {
	font-size: 1.8rem;
	line-height: 1.38;
}

.card_product__price {
	margin-bottom: 0.8rem;

	font-size: 5rem;
	font-weight: 700;
	line-height: 1;

	&:after {
		content: attr(data-price);
	}
}

.card_product--active_mod .card_product__price {
	&:after {
		content: attr(data-switch-price);
	}
}

.card_product__button {
	max-width: 16.4rem;

	@include bp($point_2, min) {
		margin: 0 auto 4.4rem;
	}
	@include bp($point_3) {
		margin: 0 auto 3.8rem;
	}
}

.card_product__btn {
	width: 100%;

	@include btn_style_black;
	@include btn_size_normal;

	transition: background-color 1s ease-in-out, color 0.6s ease-in-out,
		border-color 0.6s ease-in-out;
}

.card_product__list {
	display: flex;
	flex-direction: column;
	align-items: flex-start;

	text-align: left;

	@include bp($point_2, min) {
		margin-bottom: 3.5rem;
	}

	@include bp($point_3) {
		max-width: 33rem;

		margin: 0 auto 2.9rem;
	}

	.card_product__card--v2_mod & {
		margin-bottom: 0;
	}
}

.card_product__more {
	display: inline-flex;
	justify-content: center;
	align-items: center;

	color: $green;
	font-weight: 700;

	cursor: pointer;

	@include bp($point_2, min) {
		font-size: 1.4rem;
	}

	@include bp($point_3) {
		font-size: 1.6rem;
	}

	transition: color 0.3s ease;

	&:hover,
	&:focus {
		color: $black_2;
	}

	.card_product__card &:hover {
		&:hover,
		&:focus {
			color: $white;
		}
	}

	.card_product__card--v2_mod & {
		display: none;
	}
}

.card_product__link {
	display: flex;
	align-items: center;

	color: inherit;

	&:hover,
	&:focus {
		text-decoration: none;
	}
}

.card_product__icon {
	width: 1.1rem;
	height: 1.8rem;

	margin-left: 1rem;
}

.card_product__item {
	position: relative;

	line-height: 1.5;
	font-size: 1.2rem;

	&:not(:last-child) {
		margin-bottom: 2.4rem;
	}
	@include bp($point_2, min) {
		padding-left: 5rem;
	}

	@include bp($point_3) {
		padding-left: 5.4rem;
	}

	&:after {
		content: '';
		position: absolute;
		top: 0;

		display: block;

		height: 100%;

		background: url('../../images/primary-products/check.svg') no-repeat;
		background-size: contain;

		@include bp($point_2, min) {
			left: 2rem;
			width: 2rem;
		}

		@include bp($point_3) {
			left: 1rem;
			width: 3rem;
		}
	}
}

.primary_products__cards .swiper-wrapper {
	@include bp($point_3) {
		margin: 0 0 3rem 0;
	}
}
.primary_products__cards .swiper-slide {
	@include bp($point_3) {
		padding: 0;

		height: auto !important;
	}
}

.primary_products__cards .swiper-pagination {
	display: flex;
	align-items: center;
	justify-content: center;

	@include bp($point_2, min) {
		display: none;
	}
}

.primary_products__cards .swiper-pagination-bullet {
	display: block;

	width: 1.2rem;
	height: 1.2rem;

	border: 1px solid $black_2;
	border-radius: 50%;

	cursor: pointer;

	&:not(:last-child) {
		margin-right: 0.9rem;
	}
}

.primary_products__cards .swiper-pagination-bullet-active {
	background-color: $black_2;
}
