@import "ScssHelpers/helpers-all.scss";

.workspace_wix_tabs {
	background-color: $gray;

	@include bp($point_2, min) {
		padding: 8rem 9.1rem 5.3rem;
	}

	@include bp($point_3) {
		padding-top: 4.9rem;
		padding-bottom: 0;
	}
}

.workspace_wix_tabs__in {
	margin: 0 auto;

	width: 100%;
	max-width: 129rem;
}

.workspace_wix_tabs__content {
	@include flex_block(row, wrap);
}

.workspace_wix_tabs__content_sub_wrap {
	position: relative;

	@include bp($point_2, min) {
		flex: 1 1 auto;
		padding-bottom: 45.15%;
	}

	@include bp($point_3) {
		display: none;
	}
}

.workspace_wix_tabs__tab {
	transition: opacity 0.4s ease, visibility 0.4s ease;

	&:not(.workspace_wix_tab--active_state) {
		&:not(.workspace_wix_tabs__content_sub_wrap &) {
			display: none;
		}

		.workspace_wix_tabs__content_sub_wrap & {
			opacity: 0;
			visibility: hidden;
		}
	}

	.workspace_wix_tab--active_state {
		opacity: 1;
		visibility: visible;
	}

	.workspace_wix_tabs__content_sub_wrap & {
		height: 100%;
		@include stretch;

		@include bp($point_1, min) {
			padding-top: 0.6rem;
			padding-left: 0.8rem;
		}

		@include bp($point_1 - 1, $point_2) {
			padding-top: 0.6rem;
			padding-left: 2rem;
		}
	}
}

.workspace_wix_tabs__main-title {
	line-height: 1.3;
	color: $black_2;
	text-transform: uppercase;
	letter-spacing: 0.002em;

	@include h3;

	@include bp($point_1, $point_2) {
		font-size: 4rem;
	}

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 7.7rem;
		}
	}

	@include bp($point_3) {
		line-height: 1.11;
		&:not(:last-child) {
			margin-bottom: 4.1rem;
		}
	}

	@include bp($point_4 - 1) {
		br {
			display: none;
		}
	}
}

.workspace_wix_tabs__picture {
	@include bp($point_2, min) {
		position: relative;
		margin-right: -5rem;

		height: 100%;
		display: block;
	}
}

.workspace_wix_tabs__img {
	@include bp($point_2, min) {
		display: block;
		max-width: 100%;
		min-width: 50%;
		max-height: 100%;
		object-fit: contain;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
	}
}
