@import 'ScssHelpers/helpers-all.scss';

.popup_form {
	@include popup;
}

.popup_form__body {
	position: relative;
	width: 100%;
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
}

.popup_form__in {
	position: relative;

	margin: auto;
	width: 100%;
	max-width: 42.4rem;
	padding: 5.5rem 5.5rem 5.4rem;

	overflow: hidden;

	border-radius: 0.4rem;
	background-color: $white;

	transition: opacity 0.3s ease;

	.popup-message--active_state & {
		opacity: 0;
		// height: 0;
		pointer-events: none;
	}
}

.popup_form__close {
	position: absolute;

	width: 1.4rem;
	height: 1.4rem;

	text-transform: uppercase;

	color: transparent;
	cursor: pointer;

	&:before {
		content: '';
		position: absolute;
		top: 50%;
		left: 0;
		width: 100%;
		height: 0.1rem;

		background-color: $black_3;
		border-radius: 0.4rem;
		transform: (rotate(45deg) translate(0, 0));

		transition: background 0.3s ease;
	}
	&:after {
		content: '';
		position: absolute;
		top: 50%;
		left: 0;
		width: 100%;
		height: 0.1rem;

		background-color: $black_3;
		border-radius: 0.4rem;
		transform: (rotate(-45deg) translate(0, 0));

		transition: background 0.3s ease;
	}

	@media (any-hover: hover) {
		&:hover {
			&:before,
			&:after {
				background-color: $color_link_base;
			}
		}
	}

	@include bp($point_4, min) {
		top: 2.6rem;
		right: 2.6rem;
	}

	@include bp($point_4 - 1) {
		top: 2.4rem;
		right: 2rem;
	}
}

.popup_form__wrap {
	max-width: 29rem;
	margin: 0 auto;
}

.popup_form__title {
	text-align: center;
	color: $black_3;
	font-size: 2.4rem;
	font-weight: 700;
	letter-spacing: 0.02em;
	line-height: 1.12;

	&:not(:last-child) {
		margin-bottom: 3.2rem;
	}
}

.popup_form__col {
	&:not(:last-child) {
		margin-bottom: 3.4rem;
	}
}

.popup_form__row {
	&:not(:last-child) {
		margin-bottom: 1rem;
	}
}

.popup_form__btn_w {
	text-align: center;
}

.popup_form__btn {
	min-width: 13.6rem;
	padding-top: 2px;
	text-transform: uppercase;
}

.popup_form__file {
	padding-bottom: 0.2rem;
}

.popup_form__file_in {
	display: flex;
	align-items: center;
	cursor: pointer;
}

input.popup_form__file_input[type='file'] {
	display: none;
}

.popup_form__file_input {
}

.popup_form__file_label {
	position: relative;
	display: inline-flex;
	padding-left: 2.2rem;
	margin-top: 1rem;

	color: $green;
	font-size: 1.4rem;
	font-weight: 700;
	letter-spacing: 0.02em;

	cursor: pointer;

	@media (any-hover: hover) {
		&:hover {
			text-decoration: underline;
		}
	}
}

.popup_form__file_icon {
	position: absolute;
	top: 50%;
	left: 0;

	width: 1.4rem;
	height: 1.4rem;

	color: $green;
	transform: translateY(-50%);

	&:not(:last-child) {
		margin-right: 0.4rem;
	}
}

.popup_form__file_name {
	display: block;
	padding-top: 0.4rem;
	font-size: 1.4rem;
}

/////////////////////////////////////////

.popup_form__message {
	@include flex_block(row, nowrap, flex-start);

	position: fixed;
	top: 0;
	left: 0;

	width: 100vw;
	height: var(--full-screen);
	padding: 2rem 2.7rem;

	overflow-y: auto;

	opacity: 0;
	pointer-events: none;
	transition: opacity 0.3s;

	z-index: 1000;

	transition: opacity 0.3s ease;

	.popup-message--active_state & {
		opacity: 1;
		pointer-events: initial;
	}
}

.popup_form__message_in {
	position: relative;
	width: 100%;
	margin: auto;
	max-width: 42.4rem;

	padding: 6.6rem 5.5rem 5.8rem;

	border-radius: 0.4rem;
	background-color: $white;
}

.popup_message__close {
	position: absolute;

	width: 1.4rem;
	height: 1.4rem;

	text-transform: uppercase;

	color: transparent;
	cursor: pointer;

	&:before {
		content: '';
		position: absolute;
		top: 50%;
		left: 0;
		width: 100%;
		height: 0.1rem;

		background: $black_3;
		border-radius: 0.4rem;
		transform: (rotate(45deg) translate(0, 0));
	}
	&:after {
		content: '';
		position: absolute;
		top: 50%;
		left: 0;
		width: 100%;
		height: 0.1rem;

		background: $black_3;
		border-radius: 0.4rem;
		transform: (rotate(-45deg) translate(0, 0));
	}

	@include bp($point_4, min) {
		top: 2.6rem;
		right: 2.6rem;
	}

	@include bp($point_4 - 1) {
		top: 2.4rem;
		right: 2rem;
	}
}

.popup_form__message_wrap {
	max-width: 31.3rem;
	margin: 0 auto;
}

.popup_form__message_title {
	text-align: center;
	color: $black_3;
	font-size: 2.4rem;
	font-weight: 700;
	letter-spacing: 0.02em;
	line-height: 1.12;

	&:not(:last-child) {
		margin-bottom: 3.6rem;
	}
}

.popup_form__message_text {
	color: $black_3;
	font-size: 1.6rem;
	font-weight: 400;
	line-height: normal;
	letter-spacing: 0.02em;
	text-align: center;

	&:not(:last-child) {
		margin-bottom: 3.4rem;
	}
}

.popup_form__message_btn_w {
	text-align: center;
}

.popup_form__message_btn {
	min-width: 13.6rem;
	padding-top: 2px;
	text-transform: uppercase;
}
