@import 'ScssHelpers/helpers-all.scss';

.features_list {
	@include bp($point_2, min) {
		padding: 5rem 4.2rem;

		&:nth-child(1 of .features_list) {
			padding-top: 7.6rem;
		}

		&:nth-last-child(1 of .features_list) {
			padding-bottom: 13.2rem;
		}
	}

	@include bp($point_3) {
		padding: 4rem var(--gl-indent);
	}
}

.features_list__in {
	// max-width: 100%;
	max-width: 135.6rem;
}

.features_list__item {
	@include bp($point_2, min) {
		&:not(.features_list--reverse_mod &) {
			padding: 0 7.8rem 0 0;
		}

		.features_list--reverse_mod & {
			padding: 0 0 0 7.8rem;
		}

		&:not(:last-child) {
			margin-bottom: 7.8rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 7rem;
		}
	}
}

.features_list__row {
	@include bp($point_2, min) {
		.features_list--reverse_mod & {
			@include flex_block(row, wrap, flex-start, center);
		}

		&:not(.features_list--reverse_mod &) {
			@include flex_block(row-reverse, wrap, flex-start, center);
		}
	}
}

.features_list__col_content {
	@include bp($point_2, min) {
		width: 44.6%;

		.features_list--reverse_mod & {
			padding-right: 10rem;
		}

		&:not(.features_list--reverse_mod &) {
			padding-left: 7rem;
			padding-top: 6rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 4.4rem;
		}
	}
}

.features_list__col_picture {
	@include bp($point_2, min) {
		width: 55.4%;
	}

	@include bp($point_3) {
		margin: 0 -1rem;
	}
}

.features_list__title {
	@include title;

	color: $black_2;
	text-transform: uppercase;

	@include bp($point_2, min) {
		line-height: 1.4;

		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}

	@include bp($point_3) {
		line-height: 1.25;

		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}
}

.features_list__descrip {
	@include description;

	line-height: 1.45;
	color: $black_2;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 4.8rem;
		}
	}
}

.features_list__btn_wrap {
	@include bp($point_2, min) {
		&.features_list__btn_wrap--mobile_mod {
			display: none;
		}
	}

	@include bp($point_3) {
		text-align: center;

		&.features_list__btn_wrap--display_mod {
			display: none;
		}
	}
}

.features_list__btn {
	@include btn_size_normal;
	@include btn_style_green_3;

	min-width: 16.2rem;

	@include bp($point_2, min) {
		font-size: 1.8rem;
	}
}

.features_list__pic {
	display: block;
	width: 100%;

	@include bp($point_2, min) {
		height: 46.8rem;
	}

	@include bp($point_3) {
		height: 100%;

		&:not(:last-child) {
			margin-bottom: 6rem;
		}
	}
}

.features_list__img {
	@include contain_img;
}
