@import 'ScssHelpers/helpers-all.scss';

.plans_switcher {
	background-color: transparent;

	@include bp($point_2, min) {
		margin-bottom: 0.5rem;
	}
}

.plans_switcher__switch {
	position: relative;

	display: flex;
	justify-content: center;
	align-items: center;

	margin: 0 1.7rem;

	color: $gray_2;

	.plans_switcher__input {
		display: none;
		appearance: none;
	}
}

.plans_switcher__slider {
	position: relative;
	order: 2;

	display: inline-block;

	width: 2.6rem;
	height: 1.4rem;

	margin: 0 1.5rem;

	border-radius: 3.45rem;
	background-color: #172b29;

	cursor: pointer;

	-webkit-transition: 0.4s;
	transition: 0.4s;

	&:before {
		content: '';

		position: absolute;
		left: 2px;
		top: 50%;

		transform: translateY(-50%);

		height: 1rem;
		width: 1rem;

		border-radius: 50%;
		background-color: $white;

		-webkit-transition: 0.4s;
		transition: 0.4s;
	}
}

.plans_switcher__input {
	&:checked {
		+ {
			.plans_switcher__slider {
				background-color: #172b29;
				&:before {
					-webkit-transform: translate(1.2rem, -50%);
					-ms-transform: translate(1.2rem, -50%);
					transform: translate(1.2rem, -50%);
				}
			}
		}
	}

	&:focus {
		+ {
			.plans_switcher__slider {
				box-shadow: 0 0 1px #172b29;
			}
		}
	}
}
.plans_switcher__input:checked ~ .plans_switcher__label_left {
	color: #172b29;
}

.plans_switcher__input:checked ~ .plans_switcher__label_right {
	color: $gray_2;
}

.plans_switcher__action {
	@include bp($point_2, min) {
		margin-bottom: 0.5rem;
	}

	@include bp($point_3) {
		margin-bottom: 0.8rem;
	}
}

.plans_switcher__label_left,
.plans_switcher__label_right {
	letter-spacing: 0.02em;
	font-size: 1.8rem;

	transition: color 0.3s ease;

	@include bp($point_2, min) {
		line-height: 2.27;
	}

	@include bp($point_3) {
		line-height: 2.5;
	}
}

.plans_switcher__label_left {
	order: 3;
}

.plans_switcher__label_right {
	order: 1;
	color: #172b29;
}

.plans_switcher__sale {
	text-align: center;
	color: $orange;

	@include bp($point_2, min) {
		font-size: 1.8rem;
	}

	@include bp($point_3) {
		font-size: 1.6rem;
	}
}
