@import "ScssHelpers/helpers-all.scss";

.team_card{
    @include bp($point_5, min) {
        display: flex;
    }
}
.team_card__image{
    display: flex;
    align-items: center;

    @include bp($point_5) {
        margin-bottom: 1.4rem;
        justify-content: center;
    }
}

.team_card__picture{
    @include bp($point_5, min) {
        width: 22rem;
        height: 24.4rem;
    }
    @include bp($point_5, $point_7) {
        width: 30rem;
        height: 33.8rem;
    }
    @include bp($point_7) {
        width: 100%;
        height: auto;
    }
}

.team_card__img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.team_card__content{
    @include bp($point_5, min) {
        padding: 0 0 0 5.6rem;
    }
}

.team_card__quotes{
    margin-bottom: 0.2rem;

    &>span{    
        font-size: 6rem;
        line-height: 1.1;
        font-weight: 400;
        color: $black_2;
    }
}

.team_card__text{
    @include bp($point_5, min) {
        margin-bottom: 2.4rem;
    }
    @include bp($point_5) {
        margin-bottom: 2rem;
    }

    &>p{
        font-weight: 400;
        color: $black_2;
        
        @include bp($point_5, min) {
            font-size: 1.8rem;
            line-height: 1.4;
        }
        @include bp($point_5) {
            font-size: 1.66rem;
        }
    }
}

.team_card__name{
    margin-bottom: 0.6rem;
    &>p{
        font-size: 1.8rem;
        font-weight: 700;
        color: $black_2;
    }
}

.team_card__position{
    &>p{
        display: flex;
        align-items: center;
        
        color: $gray_3;
        &>span{
            padding-top: 3px;
            display: inline-block;
            
            font-size: 1.8rem;
            line-height: 1;
            font-weight: 400;
        }
    }
}

.team_card__social{
    margin-left: 1.2rem;
    
    width: 2.6rem;
    height: 2.6rem;

    transition: opacity 0.3s ease;

    &:hover{
        opacity: 0.8;
    }
}