.hero_wix {
	background-color: $black_2;

	@include bp($point_1, min) {
		padding: calc(4.6rem + var(--height-header)) 3.2rem 25.4rem;
	}

	@include bp($point_1 - 1) {
		padding: calc(3.2rem + var(--height-header)) 3.2rem 5.4rem;
	}
}

.hero_wix__in {
	max-width: 90rem;
	width: 100%;
	margin: 0 auto;
}

.hero_wix__content {
	text-align: center;
}

.hero_wix__title {
	@include h1;
	color: $white;
	text-transform: uppercase;
	line-height: 1.1;
	letter-spacing: 0.004em;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 3.2rem;
		}

		@include bp($point_3) {
			margin-bottom: 4.2rem;
		}
	}
}

.hero_wix__subtitle {
	font-weight: 400;
	color: $white;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 3rem;
		}

		@include bp($point_3) {
			margin-bottom: 4rem;
		}
	}

	@include bp($point_2, min) {
		font-size: 3rem;
	}

	@include bp($point_3) {
		font-size: 2.4rem;
		letter-spacing: 0.02em;
		line-height: 1.25;
	}
}

.hero_wix__text {
	font-size: 1.8rem;
	line-height: 1.43;
	color: $white;
	font-weight: 400;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 5rem;
		}

		@include bp($point_3) {
			margin-bottom: 4.7rem;
		}
	}

	@include bp($point_3) {
		letter-spacing: 0.02em;
	}
}

.hero_wix__btn {
	@include btn_style_orange_3;

	@include bp($point_2, min) {
		min-width: 16.4rem;

		@include btn_size_normal_2;
	}

	@include bp($point_3) {
		min-width: 17rem;
		@include btn_size_normal;
	}
}
