.hero_pricing {
	@include bp($point_2, min) {
		padding: calc(11.2rem + var(--height-header)) 3.2rem 14.4rem;
	}
	@include bp($point_3) {
		padding: calc(4.7rem + var(--height-header)) 3.2rem 4.4rem;
	}
}

.hero_pricing__bg {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

.hero_pricing__img {
	@include cover_img;
}

.hero_pricing__content {
	position: relative;
	text-align: center;
}

.hero_pricing__title {
	@include h3;

	color: $black_2;

	text-transform: uppercase;

	@include bp($point_2, min) {
		line-height: 1.3;
	}

	@include bp($point_3) {
		line-height: 1.33;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 3.7rem;
		}

		@include bp($point_3) {
			margin-bottom: 3.5rem;
		}
	}
}

.hero_pricing__text {
	max-width: 90rem;
	margin: 0 auto;

	@include h5;

	font-weight: 400;
	line-height: 1.45;
	color: $black_2;
}
