@import 'ScssHelpers/helpers-all.scss';

.get_started_section {
	background-color: $black_2;

	@include bp($point_2, min) {
		padding-top: 8.1rem;
		padding-bottom: 10rem;
	}

	@include bp($point_3) {
		padding-top: 8.9rem;
		padding-bottom: 9.5rem;

		border-bottom: 1px solid rgb(221, 229, 224, 0.1);
	}
}

.get_started_section__content {
	margin: 0 auto;

	text-align: center;
}

.get_started_section__title {
	@include title;

	color: $white;
	text-align: center;
	text-transform: uppercase;
	letter-spacing: 0.028em;

	&:not(:last-child) {
		margin-bottom: 0.5rem;
	}
}

.get_started_section__descr {
	font-size: 1.8rem;
	font-weight: 400;
	color: $white;

	&:not(:last-child) {
		margin-bottom: 4.8rem;
	}

	@include bp($point_2, min) {
		letter-spacing: 0.028em;
	}
}

.get_started_section__btn {
	@include btn_style_green_3;
	@include btn_size_normal;

	@include bp($point_2, min) {
		min-width: 15.6rem;
	}

	@include bp($point_3) {
		min-width: 16.2rem;
	}
}
