@import 'ScssHelpers/helpers-all.scss';

.pricing_tabs {
	clip-path: inset(1px);

	@include bp($point_2, min) {
		margin-top: -6.4rem;
		padding: 0 var(--gl-indent) 9rem;
	}

	@include bp($point_3) {
		padding: 4rem var(--gl-indent) 3.6rem;
	}
}

.pricing_tabs_in {
	max-width: 122rem;
}

.pricing_tabs__nav {
	.swiper {
		overflow: visible;
	}
}

.pricing_tabs__nav_list {
	@include flex_block(row, wrap, center, center, center);

	@include bp($point_2, min) {
		& .swiper-wrapper {
			justify-content: center;
		}
	}
}

.pricing_tabs__nav_item_wrap {
	flex-shrink: 1;
	width: auto;

	@include bp($point_3) {
		&:not(:last-child) {
			margin-right: 2.4rem;
		}
	}
}

.pricing_tabs__nav_item {
	font-size: 2.4rem;
	font-weight: 700;
	color: $white;
	font-family: $font_1;

	border-radius: 1.8rem 1.8rem 0 0;
	background-color: $black_2;

	&.pricing_tabs__nav_item--active_state {
		color: $black_2;
		background-color: white;
		pointer-events: none;

		@include bp($point_3) {
			background-color: $black_2;
			color: $white;

			&:hover {
				background-color: $black_2;
				color: $white;
			}
		}
	}

	&:not(.pricing_tabs__nav_item--active_state) {
		cursor: pointer;
	}

	@include bp($point_2, min) {
		min-width: 30.5rem;
		height: 6.4rem;
		line-height: 6.4rem;
	}

	@include bp($point_3) {
		min-width: 16.8rem;

		@include btn_style_transparent_2;
		@include btn_size_normal;
	}
}

.pricing_tabs__tab_content {
	display: none;

	&.pricing_tabs__tab_content--active_state {
		display: block;
		padding-top: 4.4rem;
	}

	@include bp($point_2, min) {
		clip-path: inset(0px);
	}
}

.pricing_tabs__plans_switcher_wrap {
	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 7rem;
		}
	}
	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 4.2rem;
		}
	}
}

.pricing_tabs__plans {
	@include flex_block(row, nowrap, center, flex-start, flex-start);

	margin: 0 auto;

	background-color: $white;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 4.1rem;
		}
	}

	@include bp($point_3) {
		margin-bottom: 2.6rem;
	}

	.pricing_tabs__tab_content[id='tab-0'] & {
		@include bp($point_2, min) {
			max-width: 91rem;
		}
	}

	.pricing_tabs__tab_content[id='tab-1'] & {
		@include bp($point_2, min) {
			// margin-left: -2.2rem;
		}
	}
}

.pricing_tabs__plans_col {
	&:nth-child(1) {
		@include bp($point_2, min) {
			padding-top: 3rem;
			padding-right: 3rem;

			.pricing_tabs__tab_content[id='tab-0'] & {
				width: 33%;
			}

			.pricing_tabs__tab_content[id='tab-1'] & {
				width: 26%;
			}
		}

		@include bp($point_3) {
			display: none;
		}
	}

	&:nth-child(2) {
		flex-grow: 1;
	}
}

.pricing_tabs__plans_title {
	font-size: 2.4rem;
	line-height: 1.26;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}
}

.pricing_tabs__plans_descr {
	font-size: 2.4rem;
	line-height: 1.26;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}
}

.pricing_tabs__plans_link_wrap {
	@include flex_block(row, wrap, flex-start, center);
}

.pricing_tabs__plans_link {
	font-size: 1.4rem;
	font-weight: 700;
	letter-spacing: 0.04rem;
}

.pricing_tabs__plans_icon {
	width: 1.1rem;
	height: 1.8rem;
	margin-left: 1rem;
	color: $green;
}

.pricing_tabs__plans_cards {
	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start, stretch, flex-start);
		z-index: 99;
	}
}

.pricing_tabs__plans_cards_mob {
	@include bp($point_2, min) {
		display: none;
	}

	@include bp($point_3) {
		@include flex_block(row, wrap, flex-start, stretch, flex-start);

		position: relative;
		z-index: 5;

		width: calc(100% + var(--gl-indent) * 2);

		margin: 0 calc(var(--gl-indent) * -1) 0;
	}
}
